/**
 * Price page scripts.
 *
 * @module Price
 */

/** Import sections scripts */
import WaitPreloader from '../../components/waitPreloader';
import { Price01 } from './sections/01_program-power';
import { Price03 } from './sections/03_about-program';

/** Class representing a website's Price page functions. Initialized only on web site's price page. */
export default class Price {
	/**
	 * Initialize Price page scripts.
	 *
	 * @static
	 */
	static init() {
		WaitPreloader.init().then(() => {
			Price01.init();
		});
		Price03.init();
	}
}
