class HelpersSection {

	constructor() {
		this.$slider = $('.helpers-slider');
	}

	initSlider() {
		this.$slider.slick({
			slidesToShow: 3,
			slidesToScroll: 1,
			dots: true,
			infinite: true,
			arrows: false,
			speed: 400,
			cssEase: 'ease',
			useTransform: true,
			adaptiveHeight: false,
			responsive: [
				{
					breakpoint: 767,
					settings: {
						slidesToShow: 1
					}
				}
			]
		});

		this.$slider.find('.slick-dots')
			.insertAfter(this.$slider)
			.attr('data-anim-stagger', 'fade-top');
	}

	init() {
		this.initSlider();
	}

}

export const About03 = new HelpersSection();
