import { CSSPlugin, TweenMax } from 'gsap';
import 'perfect-scrollbar/dist/js/perfect-scrollbar.jquery.min';
import { $body, $window, css, Resp } from '../../modules/dev/helpers';
import Fame from '../main/sections/10_fame';

export class ProgramTable extends Fame {
	constructor() {
		super();
		this.$container = $('.program-prev');
		this.$table = this.$container.find('.fame-table');
		this.$btn = $();
	}
	
	init() {
		this._checkRows();
	}
	
	_bindEvents() {
		return false;
	}
}

export class ProgramPage {
	constructor() {
		this.nextBtn = '.js-next-program';
		
		this.$hintClose = $('.program__hint-close');
		
		this.$participation = $('.participation');
		this.$programContent = $('.program__content');
		
		this.$sliderResultsFor = $('.program-results__tabs-for');
		this.$sliderResultsNav = $('.program-results__tabs-nav');
		this.$form = $('.participation__form');
		this.$formSubmit = this.$form.find('[type=submit]');
		this.$textInput = this.$form.find($('input.form-control'));
		this.$uploadBtn = $('.participation__upload-text');
		this.$uploadInput = $('.participation__upload-input');
		
		this.$content = $('.program__col:nth-child(1)');
		this.$nav = $('.program__col:nth-child(2) ul');
		this.$playBtn = $('<div class="play-btn"><svg class="icon icon-play"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-play"></use></svg></div>');
		this.mobNav = 'mobile-nav';
		
		this.$btn = $('.program-prev__btn');
		this.minRows = 3;
		this.fixHeight2 = 5;
		this.$container = $('.program-prev');
		this.$table = this.$container.find('.fame-table');
		
		this.submitFlag = false;
	}
	
	init() {
		this.initScrollbar();
		this.prepareVideo();
		this.initSlider();
		this.createSlider();
		this.initResultsSlider();
		this.initTabs();
		this.bindEvents();
		
		if (Resp.isMobile) {
			$('.modal ' + '.' + this.mobNav).addClass(this.mobNav + '_nutrition');
		} else {
			this.fixHeight();
		}
	}
	
	bindEvents() {
		//hint
		if (this.$hintClose.length) {
			this.$hintClose.on('click tap', function () {
				TweenMax.to($(this).parent(), 0.25, {
					autoAlpha: 0,
					y: 15,
					ease: Power4.easeOut
				});
			});
		}
		
		//upload
		this.$uploadBtn.on('click tap', (ev) => {
			ev.preventDefault();
			$(ev.currentTarget).siblings(this.$uploadInput).trigger('click');
		});
		this.initUpload();
		this.initValidation();
		this.initSubmit();
		
		this.$programContent.on('click tap', this.nextBtn, (e) => {
			e.preventDefault();
			this.$nav.find(`.${css.active}`).next().trigger('click');
		});
	}
	
	fixHeight() {
		if (this.$participation.length) {
			$window.on('load', () => {
				let marginSize = 42;
				if (Resp.isTablet) {
					marginSize = 32;
				}
				let colHeight = this.$participation.children().outerHeight() + this.$participation.prev().outerHeight() + marginSize + 10;
				
				this.$programContent.css({
					'min-height': colHeight
				});
			});
		}
	}
	
	initResultsSlider() {
		const activeIndex = this.$sliderResultsNav.children().filter('.' + css.active).index();
		
		this.$sliderResultsFor.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			fade: true,
			arrows: false,
			speed: 400,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			asNavFor: this.$sliderResultsNav
		});
		
		this.$sliderResultsNav.slick({
			slidesToShow: this.$sliderResultsNav.children().length,
			slidesToScroll: 1,
			asNavFor: this.$sliderResultsFor,
			dots: false,
			arrows: false,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			centerMode: true,
			focusOnSelect: true
		});
		
		this.$sliderResultsFor.slick('slickGoTo', activeIndex, false);
	}
	
	initUpload() {
		let $uploadText = this.$uploadBtn.eq(0).text().trim();
		let $uploadInput = this.$uploadInput;
		let btnCancel = 'participation__cancel';
		let $btnCancel = '<div class="participation__cancel"><svg class="icon icon-close"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-close"></use></svg></div>';
		
		$uploadInput.on('change', (ev) => {
			let $this = $(ev.currentTarget);
			
			if ($this.val()) {
				$this.siblings(this.$uploadBtn).find('span').text($this.val().replace(/C:\\fakepath\\/i, ''));
				$this.parent().append($btnCancel);
				$this.siblings('.' + btnCancel).one('click tap', function () {
					$this.val('').trigger('change');
				});
			} else {
				$this.siblings(this.$uploadBtn).find('span').text($uploadText);
				$this.siblings('.' + btnCancel).remove();
			}
		});
	}
	
	initValidation() {
		this.$textInput.on('keydown', (e) => {
			// Allow: backspace, delete, tab, escape, enter
			if ($.inArray(e.keyCode, [46, 8, 9, 27, 13, 110]) !== -1 ||
				// Allow: Ctrl+A, Command+A
				(e.keyCode === 65 && (e.ctrlKey === true || e.metaKey === true)) ||
				// Allow: home, end, left, right, down, up
				(e.keyCode >= 35 && e.keyCode <= 40)) {
				// let it happen, don't do anything
				return;
			}
			
			// Ensure that it is a number and stop the keypress
			if ((e.shiftKey || (e.keyCode < 48 || e.keyCode > 57)) && (e.keyCode < 96 || e.keyCode > 105)) {
				e.preventDefault();
			}
		});
		
		this.$textInput.on('keyup', () => {
			let validationFlag = true;
			
			this.$textInput.each((index, el) => {
				if (!$(el).val()) validationFlag = false;
			});
			
			if (!validationFlag) {
				this.$formSubmit.addClass(css.disabled);
			} else {
				this.$formSubmit.removeClass(css.disabled);
			}
		});
	}
	
	initSubmit() {
		this.$form.on('submit', (e) => {
			console.log('submit');
			if (this.submitFlag) return;
			
			let flag = false;
			
			this.$uploadInput.each((index, el) => {
				if (!$(el).val()) {
					e.preventDefault();
					
					if (flag) return;
					
					flag = true;
					
					this.$form.find(`[data-popup-target=${this.$form.data('modal-target')}]`).trigger('click');
					$('.js-challenge-submit').one('click', (e) => {
						e.preventDefault();
						this.submitFlag = true;
						window.Modal.close(this.$form.data('modal-target'));
						this.$form.submit();
						this.submitFlag = false;
					});
				}
			});
		});
	}
	
	initTabs() {
		let $tabs = $('.program__col:nth-child(2) li');
		let _this = this;
		let globalAnimationFlag = false;
		let initialAnimationFlag = false;
		let $prev = $;
		
		$tabs.on('click tap', function (e) {
			e.preventDefault();
			
			let $this = $(this);
			$prev = $tabs.filter('.' + css.active);
			
			// do nothing when clicked on active tab
			if ($this.hasClass(css.active)) return;
			
			let tabUrl = $this.data('tab-url');
			let tabId = $this.data('tab-id');
			
			if (!globalAnimationFlag) {
				globalAnimationFlag = true;
				setActiveTab($this);
				run(tabUrl, tabId);
			}
		});
		
		$tabs.bind('nextVideo', function (e) {
			e.preventDefault();
			
			let $this = $(this);
			$prev = $tabs.filter('.' + css.active);
			
			// do nothing when clicked on active tab
			if ($this.hasClass(css.active)) return;
			
			let tabUrl = $this.data('tab-url');
			let tabId = $this.data('tab-id');
			
			if (!globalAnimationFlag) {
				globalAnimationFlag = true;
				setActiveTab($this);
				run(tabUrl, tabId, true);
			}
		});
		
		/**
		 * Run/reverse animation for specified elements.
		 */
		function allAnimations() {
			const animSpeed = 0.35;
			const animPos = 15;
			
			function runAnimation() {
				// animation started. set global flag to positive
				initialAnimationFlag = true;
				
				TweenMax.to(_this.$content, animSpeed, {
					y: animPos,
					alpha: 0,
					onComplete: function () {
						initialAnimationFlag = false;
					}
				});
			}
			
			function reverseAnimation() {
				// animation started. set global flag to positive
				initialAnimationFlag = true;
				
				TweenMax.to(_this.$content, animSpeed, {
					y: 0,
					alpha: 1,
					clearProps: 'transform',
					onComplete: function () {
						globalAnimationFlag = false;
					}
				});
			}
			
			return {
				runAnimation: runAnimation,
				reverseAnimation: reverseAnimation
			};
		}
		
		/**
		 * Set active state to specified tab.
		 *
		 * @param {jQuery} $tab
		 */
		function setActiveTab($tab) {
			// remove active state from other tabs
			$tabs.removeClass(css.active);
			
			// add active state to the clicked tab
			$tab.addClass(css.active);
		}
		
		/**
		 * Wait until condition become 'false' and run function.
		 *
		 * @param {Function} condition - function that should return needed condition
		 * @param {Number} waitMs - timeout between re-check
		 * @param {Function} func - called when condition isn't 'false'
		 * @param {Object} [context] - context passed to main function
		 * @param {Array} [args] - arguments passed to main function
		 */
		function waitAndRun(condition, waitMs, func, context, args) {
			if (condition.call(null, null)) {
				setTimeout(function () {
					waitAndRun(condition, waitMs, func);
				}, waitMs);
				return;
			}
			func.apply(context, args);
		}
		
		/**
		 * Parse AJAX response. Change/append/replace data etc.
		 *
		 * @param {Object} remoteData
		 */
		function parseResponse(remoteData) {
			if (remoteData.replaces instanceof Array) {
				for (let i = 0, ilen = remoteData.replaces.length; i < ilen; i++) {
					$(remoteData.replaces[i].what).replaceWith(remoteData.replaces[i].data);
				}
			}
			if (remoteData.append instanceof Array) {
				for (let i = 0, ilen = remoteData.append.length; i < ilen; i++) {
					$(remoteData.append[i].what).append(remoteData.append[i].data);
				}
			}
			if (remoteData.content instanceof Array) {
				for (let i = 0, ilen = remoteData.content.length; i < ilen; i++) {
					$(remoteData.content[i].what).html(remoteData.content[i].data);
				}
			}
			if (remoteData.js) {
				$body.append(remoteData.js);
			}
			if (remoteData.refresh) {
				window.location.reload(true);
			}
			if (remoteData.redirect) {
				window.location.href = remoteData.redirect;
			}
		}
		
		/**
		 * Change tab.
		 *
		 * @param {String} tabUrl - AJAX request url
		 * @param {Number} tabId - ID of the clicked tab
		 * @param {Boolean} play - autoplay video
		 */
		function run(tabUrl, tabId, play = false) {
			$.ajax({
				url: tabUrl,
				method: 'get',
				dataType: 'json',
				data: tabId,
				beforeSend: function () {
					allAnimations().runAnimation();
				},
				success: function (response) {
					// wait for animation to finish
					waitAndRun(function () {
						return initialAnimationFlag;
					}, 50, function () {
						// parse response
						parseResponse(response);
						_this.prepareVideo(play);
						_this.initSlider();
						allAnimations().reverseAnimation();
					});
                    var myElement = document.querySelector('.program__col li.is-active');
                    var topPos = myElement.offsetTop;
                    document.querySelector('.program__col .ps').scrollTop = topPos;
				},
				error: function (xhr) {
					// wait for animation to finish
					waitAndRun(function () {
						return initialAnimationFlag;
					}, 50, function () {
						// console log error
						console.error('Error! ' + xhr.statusText);
						
						// reverse animation (reveal elements)
						allAnimations().reverseAnimation();
						setActiveTab($prev);
					});
				}
			});
		}
	}
	
	initScrollbar() {
		this.$nav.perfectScrollbar({
			wheelPropagation: true,
			swipePropagation: true,
			swipeEasing: false
		});
	}
	
	prepareVideo(play = false) {
		let $video = this.$content.find('video');
		
		if ($video.length) {
			$video.each((index, el) => {
				let $currentTarget = $(el);
				let $playBtn = this.$playBtn.clone();
				
				$currentTarget.wrap('<div class="program__video"></div>');
				$playBtn.insertAfter($currentTarget);

				this.initPlayBtn($playBtn);
				
				$currentTarget.get(0).addEventListener('ended', () => {
					let $tabs = $('.program__col:nth-child(2) li');
					
					$tabs.filter('.' + css.active).next().trigger('nextVideo');
				});
			});
		}
		
		if (play && $video.length) {
			setTimeout(() => {
				if ($video.closest('.program-slider').length) {
					if ($video.closest('.slick-slide.slick-current').length) {
						$video.eq(0).siblings(this.$playBtn.get(0)).trigger('click');
					}
				} else {
					$video.eq(0).siblings(this.$playBtn.get(0)).trigger('click');
				}
			}, 0);
		}
	}
	
	initSlider() {
		let $sliderFor = this.$content.find('.program-slider__for');
		let $sliderNav = this.$content.find('.program-slider__nav');
		
		if ($sliderFor.length && $sliderNav.length) {
			$sliderFor.children().each(function () {
				$(this).wrap('<div></div>');
			});
			
			$sliderNav.children().each(function () {
				$(this).wrap('<div class="program-slider__nav-image"></div>');
			});
			
			$sliderFor.slick({
				lazyLoad: 'progressive',
				slidesToShow: 1,
				slidesToScroll: 1,
				arrows: true,
				speed: 400,
				swipe: false,
				draggable: false,
				cssEase: 'ease',
				adaptiveHeight: true,
				asNavFor: $sliderNav
			});
			
			$sliderNav.slick({
				slidesToShow: 5,
				slidesToScroll: 1,
				asNavFor: $sliderFor,
				dots: false,
				arrows: true,
				swipe: false,
				draggable: false,
				cssEase: 'ease',
				centerMode: true,
				centerPadding: '37px',
				focusOnSelect: true,
				responsive: [
					{
						breakpoint: 1200,
						settings: {
							slidesToShow: 3,
							centerPadding: '36px'
						}
					}, {
						breakpoint: 768,
						settings: {
							slidesToShow: 2,
							centerMode: false,
							centerPadding: '36px'
						}
					}
				]
			});
		}
	}
	
	createSlider() {
		const $sliderNav = $('.program-prev__fame-table-nav');
		
		if (!$sliderNav.length) return;
		
		const $slider = $sliderNav.next();
		const navLength = $sliderNav.children().length;
		const $activeItem = $sliderNav.children().filter('.' + css.active);
		const activeIndex = $activeItem.length ? $activeItem.index() : 0;
		
		$slider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			fade: true,
			arrows: false,
			speed: 400,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			asNavFor: $sliderNav
		});
		
		$sliderNav.slick({
			slidesToShow: navLength,
			slidesToScroll: 1,
			asNavFor: $slider,
			dots: false,
			arrows: false,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			centerMode: true,
			focusOnSelect: true
		});
		
		$slider.slick('slickGoTo', activeIndex, false);
		this._bindEvents();
	}
	
	_bindEvents() {
		this.$btn.children().on('click tap', (e) => {
			e.preventDefault();
			this.$btn
				.children().children().last()
				.data('active', true);
			if (this.$btn.data('active') !== true) {
				this.$btn.data('active', true);
				this._showAllRows();
			} else {
				this.$btn.data('active', false);
				this._hideAllRows();
			}
		});
	}
	
	_showAllRows() {
		const $sliderNav = $('.program-prev__fame-table-nav');
		const $slider = $sliderNav.next();
		
		if (!Resp.isDesk) {
			this.$container.find('.slick-list').height('auto');
		}
		this.$table.each((index, el) => {
			let fixFlag = false;
			let $container = $(el).find('.fame-table__body');
			let $rows = $(el).find('.fame-table__row');
			if ($rows.length > this.minRows) {
				let activeItem = $container.find('.is-active');
				if (activeItem.length && activeItem.index() >= this.minRows) {
					fixFlag = true;
				}
				let startHeight = $container.css({ overflow: 'hidden' }).height();
				let itemHeight = startHeight / this.minRows;
				itemHeight = fixFlag ? (startHeight - this.fixHeight2) / (this.minRows + 1) : itemHeight;
				let targetHeight = $rows.length * itemHeight;
				
				$(el).addClass(css.active);
				$rows.addClass('is-flex');
				
				$container.css({ height: startHeight });
				TweenMax.to($container, 0.5, {
					height: targetHeight,
					ease: Power2.easeInOut,
					onComplete: () => {
						if (!Resp.isDesk) {
							$slider.slick('setPosition');
						}
					}
				});
			}
		});
	}
	
	_hideAllRows() {
		const $sliderNav = $('.program-prev__fame-table-nav');
		const $slider = $sliderNav.next();
		
		if (!Resp.isDesk) {
			this.$container.find('.slick-list').height('auto');
		}
		this.$table.each((index, el) => {
			let fixHeight = 5;
			let fixFlag = false;
			let $container = $(el).find('.fame-table__body');
			let $rows = $(el).find('.fame-table__row');
			if ($rows.length > this.minRows) {
				let activeItem = $container.find('.is-active');
				if (activeItem.length && activeItem.index() >= this.minRows) {
					fixFlag = true;
				}
				let startHeight = $container.css({ overflow: 'hidden' }).height();
				let itemHeight = startHeight / $rows.length;
				let targetHeight = this.minRows * itemHeight;
				targetHeight = fixFlag ? (this.minRows + 1) * itemHeight + fixHeight : targetHeight;
				
				$(el).removeClass(css.active);
				
				TweenMax.to($container, 0.5, {
					height: targetHeight,
					ease: Power2.easeInOut,
					onComplete: () => {
						$rows.eq(this.minRows - 1).nextAll().css({ display: 'none' });
						$rows.removeClass('is-flex');
						if (!Resp.isDesk) {
							$slider.slick('setPosition');
						}
					}
				});
			}
		});
	}
	
	initPlayBtn(el) {
		el.on('click tap', function () {
			let $this = $(this);
			
			$this.addClass('is-hidden');
			if ($this.siblings('video').length) {
				$this.siblings('video').attr('controls', 'true').get(0).play();
				$this.siblings('video').focus();
				$this.siblings('video').blur();
      }
		});
	}
}
