import { TweenMax, CSSPlugin } from 'gsap';
import { css, Resp } from '../modules/dev/helpers';
import Popup from 'vintage-popup/dist/vintage-popup.min';
import { Select } from './selectInit';

export class CTabs {
	constructor() {
		this.$tabNav = $('.c-tabs__tabs-nav').find('.c-tabs__tabs-el');
		this.$tabItemContainer = $('.c-tabs__tabs-for');
		this.$tabItem = this.$tabItemContainer.find('.c-tabs__tab');
		this.$select = $('.cab-content__head-select');
	}
	
	init() {
		this.bindEvents();
	}
	
	bindEvents() {
		if (!Resp.isMobile && !this.$tabNav.hasClass('js-disabled')) {
		} else {
			this.initMobile();
		}
	}
	
	initMobileSelect(menu) {
		if (this.$select.length) {
			menu.parent().prepend(this.$select);
			menu.closest('.mobile-nav').addClass('mobile-nav_personal');
			new Select(this.$select.get(0));
		}
	}
	
	initMobile() {
		// dots
		let dots = `<div class="c-tabs__dot c-tabs__dot_1"></div><div class="c-tabs__dot c-tabs__dot_2"></div><div class="c-tabs__dot c-tabs__dot_3"></div>`;
		this.$tabNav.parent().append(dots);
		
		// prepare modal
		let mobMenu = `<div class="modal modal_modal" data-popup-id="tabsPopup" tabindex="-1" role="dialog"><div class="modal__container"><div class="modal__close js-modal-close"><svg class="icon icon-close"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-close"></use></svg></div><div class="mobile-nav"><div class="mobile-nav__content"></div><div class="mobile-nav__btn"><button class="btn btn_red mobile-nav__close-btn js-nav-submit"><div class="btn__bg"></div><span class="btn__text">ок</span></button></div></div></div></div>`;
		let $link;
		let $menu = $('.content').append(mobMenu).find('.mobile-nav__content');
		
		//ini select
		this.initMobileSelect($menu);
		
		let $submit = $menu.siblings().find('.js-nav-submit');
		
		// init modal
		Popup.expose($);
		this.$tabNav.popup({
			targetPopupId: 'tabsPopup',
			closeOnEsc: false,
			closeBtnSelector: '.js-modal-close'
		});
		let modalMenu = $('[data-popup-id="tabsPopup"]').data('popup');
		
		this.$tabNav.on('click tap', (ev) => {
			ev.preventDefault();
			copyLinks(this);
		});
		
		$submit.on('click tap', () => {
			let activeIndex = $menu.find('.is-active').index();
			let currentIndex = this.getActiveIndex();
			if (activeIndex !== currentIndex) {
				if (!this.$tabNav.parent().hasClass('js-disabled')) {
					this.changeTab(currentIndex, activeIndex);
				} else {
					window.location.replace($link.eq(activeIndex).attr('href'));
				}
			}
			modalMenu.close();
		});
		
		function copyLinks(el) {
			$link = $menu.html(el.$tabNav.clone().removeClass('c-tabs__tabs-el').addClass('mobile-nav__el')).children();
			
			$link.on('click tap', (ev) => {
				ev.preventDefault();
				$link.removeClass('is-active');
				$(ev.currentTarget).addClass('is-active');
			});
		}
	}
	
	getActiveIndex() {
		let activeIndex = 0;
		
		this.$tabNav.each(function () {
			if ($(this).hasClass(css.active)) {
				activeIndex = $(this).index();
			}
		});
		
		return activeIndex;
	}
	
	changeTab(currentIndex, nextIndex) {
		let _this = this;
		let speed = 0.3;
		let $currentTabNav = this.$tabNav.eq(currentIndex);
		let $nextTabNav = this.$tabNav.eq(nextIndex);
		let $currentTab = this.$tabItem.eq(currentIndex);
		let $nextTab = this.$tabItem.eq(nextIndex);
		
		$currentTabNav.removeClass(css.active);
		$nextTabNav.removeClass(css.disabled).addClass(css.active);
		TweenMax.to($currentTab, speed, {
			autoAlpha: 0,
			y: 15,
			clearProps: 'transform',
			onComplete: function () {
				let currentHeight = _this.$tabItemContainer.outerHeight();
				TweenMax.set(_this.$tabItemContainer, { height: currentHeight });
				$(this.target).hide();
				TweenMax.set($nextTab, { autoAlpha: 1 });
				$nextTab.show();
				TweenMax.staggerFromTo($nextTab.children().children(), speed, {
					autoAlpha: 0,
					y: -15
				}, {
					autoAlpha: 1,
					y: 0
				}, speed / 2);
				TweenMax.set(_this.$tabItemContainer, { height: 'auto' });
				TweenMax.from(_this.$tabItemContainer, speed, { height: currentHeight });
			}
		});
	}
}

export default new CTabs();
