import { Resp } from '../modules/dev/helpers';

class Panel {
  constructor () {
    this.$panelNotificationIcon = $('.panel__nav-btn:last-child').find('i');
    this.$tooltip = $('.panel__tooltip');
    this.$tooltipClose = $('.panel__tooltip-close');

    this.bindEvents();
  }

  bindEvents () {
    this.$tooltipClose.on('click', (e) => {
      $(e.currentTarget).closest(this.$tooltip).addClass('is-hide');
    });

    if (Resp.isMobile) {
      if (this.$tooltip.length) {
        if (!this.$tooltip.hasClass('is-hide')) {
          this.$panelNotificationIcon.addClass('is-pulse');
        }
      }
    }
  }
}

export default new Panel();
