import 'perfect-scrollbar/dist/js/perfect-scrollbar.jquery.min';
import '../modules/dep/jquery.selectric.min';
import 'select2';

export class Select {
	
	constructor(el) {
		this.$el = $(el);
	}
	
	init() {
		this.$el.selectric({
			onOpen: function () {
				$('.selectric-items').perfectScrollbar('update');
			}
		});
		$('.selectric-items').perfectScrollbar();
	}
}

export class Select2 {
	
	constructor(el) {
		this.$el = $(el);
	}
	
	init() {
		const textFind = this.$el.data('find');
		this.$el.select2({
			minimumResultsForSearch: 0,
			allowClear: false,
			language: {
				noResults: function () {
					return textFind;
				}
			},
			escapeMarkup: function (markup) {
				return markup;
			}
		})
			.on('select2:opening', function () {
				$(this).closest('.form-group').removeClass('has-error');
			});
	}
}

export const Selectric = new Select('.js-select');
export const select2 = new Select2('.js-select2');

window.refreshSelect = function () {
	new Select('.js-select').init();
	new Select2('.js-select2').init();
};
