import { TimelineMax } from 'gsap';
import Header from '../modules/dev/animation/header';
import { AnimStagger } from '../modules/dev/animation/stagger';
import { ELEMENTS } from '../modules/dev/elements';
import { $body, $scrolledElements, $window, css, currentPage } from '../modules/dev/helpers';

export class Preloader {
	constructor() {
		this.preloaderOff = false;
		this.resolved = false;
		this.tl = new TimelineMax();
	}
	
	init() {
		return new Promise(resolve => {
			if (this.preloaderOff === true) {
				// add body scroll
				$scrolledElements.removeClass(css.overflow);
				
				// start header animation
				Header.playAnim();
				
				// start stagger
				AnimStagger.init();
				
				// resolve
				this.resolved = true;
				
				resolve();
			} else if (currentPage !== 'main') {
				$window.on('load', () => {
					$body.removeClass(css.load);
					
					// add body scroll
					$scrolledElements.removeClass(css.overflow);
					
					// start header animation
					Header.playAnim();
					
					// start stagger
					AnimStagger.init();
					
					// resolve
					this.resolved = true;
					
					resolve();
				});
			} else {
				$window.on('load', () => {
					setTimeout(() => {
						if (currentPage === 'main') {
							// scroll to top before preloader animation
							$scrolledElements.removeClass(css.overflow);
							$scrolledElements.scrollTop(0);
							$scrolledElements.addClass(css.overflow);
						}
						
						// resolver
						(new Preloader())._animate().then(() => {
							// add body scroll
							$scrolledElements.removeClass(css.overflow);
							
							// start header animation
							Header.playAnim();
							
							// start stagger
							AnimStagger.init();
							
							// resolve
							this.resolved = true;
							
						}).then(resolve);
					}, 100);
				});
			}
		});
	}
	
	_animate() {
		this.tl
			.to(ELEMENTS.preloader.$boxTop, 0.75, {
				y: '-100%',
				ease: Power4.easeIn
			}, 0)
			.to(ELEMENTS.preloader.$boxBot, 0.75, {
				y: '100%',
				ease: Power4.easeIn
			}, 0)
			.addLabel('verticalEnd')
			.to(ELEMENTS.preloader.$boxLeft, 1, {
				x: '-100%',
				ease: Power4.easeInOut
			}, 'verticalEnd')
			.to(ELEMENTS.preloader.$boxRight, 1, {
				x: '100%',
				ease: Power4.easeInOut
			}, 'verticalEnd')
			.set(ELEMENTS.preloader.$container, {
				css: {
					display: 'none'
				}
			});
		
		return new Promise(resolve => {
			this.tl.vars.onComplete = function () {
				resolve();
			};
		});
	}
}

export default new Preloader();
