import { TweenMax, CSSPlugin } from 'gsap';
import { Resp, $body } from '../../modules/dev/helpers';

export class NutritionPage {
	constructor() {
    this.$row = $('.nutrition__row');
		this.$col = $('.nutrition__row-list-col');
		this.$card = $('.nutrition-card');
		this.$image = $('.nutrition-card__image');
		this.mobNav = 'mobile-nav';
		this.tooltip = '.nutrition__row-list-col-tooltip';
		this.$tooltip = null;
		this.state = false;
		this.imageSize = {};
	}

	init() {
		this.bindEvents();
		this.initVariant();
    if (Resp.isDesk) this.compareCards();
		if (Resp.isMobile) {
			$('.modal ' + '.' + this.mobNav).addClass(this.mobNav + '_nutrition');
		}
	}

	compareCards() {
		this.$row.each((index, el) => {
			if (index % 2 !== 0) return;
			const $row = $(el);
			const $currentCol = $row.find(this.$col).eq(0);
			const $nextCol = $row.next().find(this.$col).eq(0);

			let currentHeight = $currentCol.height();
			let nextHeight = $nextCol.height();

			if (currentHeight > nextHeight) {
				$nextCol.css('height', currentHeight);
			} else {
				$currentCol.css('height', nextHeight);
			}
		});
	}

	bindEvents() {
		if (Resp.isDesk) {
			this.initTooltips();
		}
	}

	initVariant() {
		this.$col.each((index, el) => {
			if ($(el).hasClass('is-variant')) {
				$(el).parent().addClass('is-variant');
			}
		});
	}

	initTooltips() {
		TweenMax.set($(this.tooltip), { x: '-50%', y: 10 });

		this.$image.on('mouseenter', (ev) => {
			this.state = false;
			let $this = $(ev.currentTarget);
			let imageOffset = $this.offset();
			this.imageSize = {
				width: $this.width(),
				height: $this.height()
			};

			if ($this.closest(this.$col).index() === 0) {
				this.state = 'first';
			} else if ($this.closest(this.$col).index() >= 3) {
				this.state = 'last';
			}

			let $currentTooltip = $this.closest(this.$card).siblings(this.tooltip);
			createTooltip(this, imageOffset, this.imageSize, $currentTooltip, this.state);
		});

		this.$image.on('mouseout', () => {
			let currentTooltip = this.$tooltip;
			let currentState = this.state;
			let currentImageSize = this.imageSize;
			let x = '-50%';

			if (currentState === 'first') {
				x = -1 - currentImageSize.width / 2;
			} else if (currentState === 'last') {
				x = -currentTooltip.outerWidth() + currentImageSize.width / 2 + 1;
			}

			TweenMax.to(currentTooltip, 0.25, {
				alpha: 0,
				x: x,
				y: 10 - currentTooltip.outerHeight(),
				ease: Power2.easeIn,
				onComplete: () => {
					currentTooltip.remove();
				}
			});
		});

		function createTooltip(that, imageOffset, imageSize, $currentTooltip, state) {
			let x = '-50%';

			that.$tooltip = $(`<div class="nutrition-tooltip">${$currentTooltip.html()}</div>`);

			$body.append(that.$tooltip);

			that.$tooltip.css({
				position: 'absolute',
				top: imageOffset.top + imageSize.height / 2,
				left: imageOffset.left + imageSize.width / 2
			});

			if (state === 'first') {
				x = -1 - imageSize.width / 2;
				that.$tooltip.addClass('is-left');
			} else if (state === 'last') {
				x = -that.$tooltip.outerWidth() + imageSize.width / 2 + 1;
				that.$tooltip.addClass('is-right');
			}

			TweenMax.set(that.$tooltip, { x: x, y: 10 - that.$tooltip.outerHeight() });
			TweenMax.to(that.$tooltip, 0.25, {
				alpha: 1, x: x,
				y: 0 - that.$tooltip.outerHeight(),
				ease: Power2.easeIn
			});
		}
	}
}

export default new NutritionPage();
