import Preloader from '../../../components/preloader';
import WrapHidden from '../wrapHidden';
import 'gsap';
import { Resp } from '../helpers';

export class FirstScreen {

	constructor() {
		this.$container = $('.js-first-screen');
		this.$title = this.$container.find('.title-lg, .subtitle-lg');
		this.$btn = this.$container.find('.first-section__btn');
		this.$back = this.$container.find('.first-section__back');
		this.$el = this.$container.find('.first-section__center').children().not(this.$btn).not(this.$back);
		this.animSpeed = 0.75;
		this.staggerDelay = 0.15;
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (this.$container.length) {
			if (Resp.isDesk) {
				this.createAnim();
				Preloader.init().then(() => {
					this.playAnim();
				});
			} else {
				Preloader.init();
			}
		}
	}

	createAnim() {
		// wrap elements
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addWrapClone(this.$el);
		if (this.$btn.length) {
			WrapHidden.addWrapClone(this.$btn, false);
			this.$el = this.$el.add(this.$btn);
		}

		// add start label
		this.tl.addLabel('start');

		// add title
		this.tl
			.staggerFrom(this.$title, this.animSpeed, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'transform, overflow'
			}, 1.5 * this.staggerDelay, 'start')
			.staggerFrom(this.$title.children(), this.animSpeed, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform, overflow'
			}, 1.5 * this.staggerDelay, 'start')
			.addLabel('titleEnd');

		// add elements
		this.tl
			.staggerFrom(this.$el, this.animSpeed, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform, overflow'
			}, this.staggerDelay, 'titleEnd-=0.35')
			.staggerFrom(this.$el.parent(), this.animSpeed, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'transform, overflow'
			}, this.staggerDelay, 'titleEnd-=0.35')
			.addLabel('elEnd');

		// add back button
		this.tl.from(this.$back, this.animSpeed, {
			alpha: 0,
			x: -100,
			ease: Power3.easeOut
		}, 'elEnd-=0.35');
	}

	playAnim() {
		this.tl.play();
	}

}

export default new FirstScreen();
