import Trainer from '../../main/sections/03_trainer';
import WrapHidden from '../../../modules/dev/wrapHidden';

export default class TrainerAbout extends Trainer {

	constructor() {
		super();
		this.$p = this.$title.nextAll('p');
		this.$socials = this.$container.find('.socials');
	}

	_wrapHidden() {
		WrapHidden.addWrap(this.$socials);
		return super._wrapHidden();
	}

	_createAnimation() {
		this.$canvas = this.$container.find('#outputAlphaVideo');
		this.tl
			.addLabel('start')
			.from(this.$titleLarge.children(), 1.25, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$titleLarge, 1.25, {
				x: '-25%',
				ease: Power3.easeOut
			}, 'start')
			.addLabel('titleLargeEnd')
			.from(this.$desc.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'titleLargeEnd-=0.5')
			.from(this.$desc, 1, {
				x: '-75%',
				ease: Power3.easeOut,
			}, 'titleLargeEnd-=0.5')
			.addLabel('descEnd')
			.from(this.$canvas, 2, {
				alpha: 0,
				x: 200,
				ease: Power3.easeOut
			}, 'start')
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'descEnd-=0.5')
			.from(this.$title, 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'descEnd-=0.5')
			.addLabel('titleEnd')
			.from(this.$p, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'titleEnd-=0.5')
			.from(this.$p.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'titleEnd-=0.5')
			.addLabel('pEnd')
			.from(this.$socials, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'pEnd-=0.5')
			.from(this.$socials.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'pEnd-=0.5')
			.addLabel('socialsEnd');
	}

}

export const About01 = new TrainerAbout();
