import { $scrolledElements, $window, css, currentPage, Resp, throttle } from '../modules/dev/helpers';

class ToTop {
	constructor() {
		this.$btn = $('.to-top');
		
		if (this.$btn.length) this.init();
	}
	
	init() {
		this.bindEvents();
		this.checkScroll();
	}
	
	checkScroll() {
		const windowHeight = $window.height();
		
		checkScroll.apply(this);
		
		$window.on('scroll', throttle(() => {
			checkScroll.apply(this);
		}, 250, this));
		
		function checkScroll() {
			if ($window.scrollTop() > windowHeight * 2.5) {
				this.$btn.addClass(css.active);
			} else {
				this.$btn.removeClass(css.active);
			}
		}
	}
	
	bindEvents() {
		this.$btn.on('click', () => {
			$scrolledElements.animate({ scrollTop: 0 }, 1500);
		});
	}
}

export default new ToTop();
