import Popup from 'vintage-popup/dist/vintage-popup.min';
import { css } from '../../modules/dev/helpers';

export class SettingsPage {
	constructor() {
		// tab #1
		this.$form = $('.form_personal-data');
		this.$formSubmit = this.$form.find('.btn');
		this.$input = this.$form.find('.form-control-text, select');
		this.group = '.form-group';
		
		// tab#2
		this.$form2 = $('.form_settings-promo');
		this.$formSubmit2 = this.$form2.find('.btn');
	}
	
	init() {
		this.validatePromo();
	}
	
	validatePromo() {
		// init modal
		Popup.expose($);
		this.$formSubmit2.popup({
			targetPopupId: 'promoTy',
			closeOnEsc: false,
			closeBtnSelector: '.js-modal-close',
			openOnClick: false
		});
	}
}

export default new SettingsPage();
