import { TimelineMax } from 'gsap';
import SCROLL_TRIGGER_ANIMATIONS from '../modules/dev/animation/helpers/_scrollTriggerAnimations';
import { Resp } from '../modules/dev/helpers';

class MainSocials {
	
	constructor () {
		this.$container = $('.main-socials');
		this.$item = this.$container.find('.main-socials__list-item');
		this.$btn = this.$container.find('.main-socials__list-btn');
		this.tl = new TimelineMax({ paused: true });
	}
	
	init () {
		if (this.$container.length) {
			
			if (Resp.isDesk) {
				this._createAnimation();
				this._playAnimation();
			}
			
		}
	}
	
	_createAnimation () {
		this.tl
			.addLabel('start')
			
			//item
			.set(this.$item, {
				css: {
					className: '+=is-animate'
				}
			}, 'start+=0.5')
			.addLabel('itemEnd')
			
			//button
			.staggerFrom(this.$btn, 1.5, {
				x: -350,
				ease: Power4.easeOut
			}, 0.25, 'itemEnd+=0.75')
			.addLabel('btnEnd');
	}
	
	_playAnimation () {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			triggerHook: 0.85,
			onStart: () => {
				this.tl.play();
			}
		});
	}
	
}

export default new MainSocials();
