/**
 * Results page scripts.
 *
 * @module Results
 */

/** Import scripts */
import WaitPreloader from '../../components/waitPreloader';
import { Results01 } from './sections/01_results-section';

/** Class representing a website's Results page functions. Initialized only on web site's results page. */
export class Results {

	init() {
		Results01.init();
		WaitPreloader.init().then(() => {
			Results01.playAnim();
		});
	}

}

export default new Results();
