import { css } from '../../modules/dev/helpers';

export class PersonalPage {
	constructor() {
		this.$form1 = $('.personal').find('form');
		this.$select = $('.cab-content__head-select');
		this.$clearBtn = $('.photoload__card-close');
	}
	
	init() {
		this.bindEvents();
		this.validateTab1();
	}
	
	bindEvents() {
		this.$select.on('change', function () {
			window.location = $(this).find('option:selected').data('href');
		});
		
		this.$clearBtn.on('click tap', function (e) {
			e.preventDefault();
			e.stopPropagation();
			$(this).siblings('img').remove();
			$(this).siblings('input[type=hidden]').val('').trigger('change');
			$(this).parent().removeClass(css.image);
		});
	}
	
	validateTab1() {
		let $form1 = this.$form1;
		let $input = $form1.find('input').not(':disabled');
		let $disabledInput = $form1.find('input').slice(-1);
		
		$input.on('change keyup', () => {
			if (getFormState()) {
				enableInput();
			} else {
				disableInput();
			}
		});
		
		function getFormState() {
			let state = true;
			$input.each(function () {
				if ($(this).val() === 'false' || !$(this).val()) {
					state = false;
				}
			});
			
			return state;
		}
		
		function enableInput() {
			$disabledInput.addClass(css.enabled);
		}
		
		function disableInput() {
			$disabledInput.removeClass(css.enabled);
		}
	}

	setResult(result) {
		let $disabledInput = this.$form1.find('input').filter('[data-input="result"]');
		
		$disabledInput
			.val(result)
			.addClass(css.fill)
			.attr('disabled', false);
	}
	
	removeResult() {
		let $disabledInput = this.$form1.find('input').filter('[data-input="result"]');
		
		$disabledInput
			.val('')
			.removeClass(css.fill)
			.attr('disabled', true);
	}
}

export default new PersonalPage();
