import {ELEMENTS} from '../modules/dev/elements';
import {TimelineMax, TweenMax} from 'gsap';
import {Resp, $body, css, $window} from '../modules/dev/helpers';

export class Menu {
	constructor() {
		this.$menu = ELEMENTS.header.$menu;
		this.$toggle = ELEMENTS.header.$menuToggle;
		this.$colAnim = ELEMENTS.header.$colAnim;
		this.$close = ELEMENTS.header.$close;
		this.$menuLastCol = this.$menu.find('.menu__col:last-child');
		this.$socials = this.$menu.find('.socials');
		this.tl = new TimelineMax({ paused: true });
		this.scrollTop = 0;
	}

	init() {
		if (Resp.isDesk) {
			this._createTimeline();
			this._bindEvents();
		} else {
			this.createMobileTimeline();
			this._bindEvents();
		}

		if (Resp.isMobile) {
			this.createMobileScroll();
		}

		//move socials to end
		if (Resp.isMobile) {
			this.$socials.insertAfter(this.$menuLastCol).wrap('<div class="menu__col"></div>');
		}
	}

	createMobileScroll() {
		this.$menu.find('.menu__container')
			.wrapInner('<div class="menu__container-scroll"></div>');
	}

	_createTimeline() {
		// hide menu
		TweenMax.set(this.$menu, {
			alpha: 0
		});

		this.tl
			.fromTo(this.$menu, 0.5, {
				y: '-100%'
			}, {
				y: '0%',
				ease: Power1.easeOut
			})
			.addLabel('menuOpened')
			.staggerFromTo(this.$colAnim, 1, {
				x: '130%'
			}, {
				x: '0%',
				clearProps: 'all',
				ease: Power2.easeOut
			}, 0.15, 'menuOpened-=0.05')
			.staggerFromTo(this.$colAnim, 0.5, {
				alpha: 0,
			}, {
				alpha: 1,
				ease: Power1.easeOut
			}, 0.15, 'menuOpened-=0.05');
	}

	createMobileTimeline() {
		// hide menu
		TweenMax.set(this.$menu, {
			alpha: 0
		});

		this.tl
			.fromTo(this.$menu, 0.5, {
				y: '-100%'
			}, {
				y: '0%',
				ease: Power1.easeOut
			})
			.addLabel('menuOpened');

		this.tl.onComplete = () => {
			$body.addClass(css.menuActive);
		};
	}

	_bindEvents() {
		//open menu
		this.$toggle.on('click tap touchstart', (e) => {
			e.preventDefault();
			this.open();
		});

		//close menu
		this.$close.on('click tap touchstart', (e) => {
			e.preventDefault();
			this.close();
		});
	}

	beforeOpen() {
		this.scrollTop = $window.scrollTop();
	}

	beforeClose() {
		$body.scrollTop(this.scrollTop);
	}

	open() {
		this.beforeOpen();
		
		// menu's opacity off
		TweenMax.set(this.$menu, {
			alpha: 1
		});
		this.tl.vars.onComplete = () => {
			$body.addClass(css.menuActive);
		};
		
		this.tl
			.timeScale(1)
			.play();
	}

	close() {
		$body.removeClass(css.menuActive);
		this.beforeClose();
		this.tl
			.timeScale(1.75)
			.reverse();

		this.tl.vars.onReverseComplete = () => {
			// menu's opacity on
			TweenMax.set(this.$menu, {
				alpha: 0
			});
		};
	}
}

export default new Menu();
