import { TimelineMax, TweenMax } from 'gsap';
import { $body, $window } from '../modules/dev/helpers';

export class Tooltip {
	constructor() {
		this.$tooltip = $('.js-tooltip');
		
		this.init();
	}
	
	init() {
		this.initTooltip();
	}
	
	initTooltip() {
		let tooltipRight = 'tooltip_right';
		let tooltipLeft = 'tooltip_left';
		
		this.$tooltip.each((index, el) => {
			let $btn = $(el);
			let content = $btn.data('tooltip-content');
			let $tooltip = $(`<div class="tooltip">${content}</div>`);
			let arrowPosition = $btn.data('tooltip-arrow') || 'default';
			let tooltipSize = $btn.data('tooltip-size') || false;
			
			$btn.on('mouseenter', function () {
				let $this = $(this);
				let offset = $this.offset();
				
				$body.append($tooltip);
				
				if (tooltipSize !== false) {
					$tooltip.css({
						'max-width': 340
					});
				}
				
				let arrowY = $tooltip.outerHeight() + 20;
				let arrowX = $tooltip.outerWidth() / 2 - $this.outerWidth() / 2;
				
				switch (arrowPosition) {
					case 'left':
						$tooltip.addClass(tooltipLeft);
						arrowX = 20 - $this.outerWidth() / 2;
						break;
					case 'right':
						$tooltip.addClass(tooltipRight);
						arrowX = $tooltip.outerWidth() - $this.outerWidth() - 13;
						break;
					default: break;
				}
				
				let top = offset.top - arrowY;
				let left = offset.left - arrowX;
				
				//correct position in screen
				if (left < 0) {
					left = 0;
				} else if (left + $tooltip.outerWidth() > $window.width()) {
					left = $window.width() - $tooltip.outerWidth();
				}
				
				$tooltip.css({
					top: top,
					left: left
				});
				TweenMax.fromTo($tooltip, 0.25,
					{ alpha: 0, y: 15 },
					{ alpha: 1, y: 0, ease: Power4.easeOut }
				);
			});
			
			$btn.on('mouseleave', function () {
				TweenMax.to($tooltip, 0.25,
					{
						alpha: 0, y: 15, ease: Power4.easeOut,
						onComplete: function () {
							$(this.target).remove();
						}
					}
				);
			});
		});
	}
}

export default new Tooltip();


