import '../modules/dep/jquery.plugin.min';
import '../modules/dep/jquery.countdown.min';
import '../modules/dep/jquery.countdown-ru';
import moment from 'moment';
import 'moment-timezone';

export class Countdown {

	constructor() {
		this.$container = $('.js-countdown');
	}

	init() {
		if (this.$container.length) {
			this._createCountdown();
		}
	}

	_createCountdown() {
		let currentTime = moment();
		let targetTime = moment().day(8).startOf('day').tz('Europe/Kiev');
		let time = targetTime.diff(currentTime, 'seconds');
		time = moment.duration(time);
		time = msToTime(time).split(':');
		let $visible = $('<div class="js-countdown-visible"><span class="countdown-row"><span class="countdown-section"><span class="countdown-amount"></span><span class="countdown-period"></span></span><span class="countdown-section"><span class="countdown-amount"></span><span class="countdown-period"></span></span><span class="countdown-section"><span class="countdown-amount">20</span><span class="countdown-period">Минут</span></span><span class="countdown-section"><span class="countdown-amount">30</span><span class="countdown-period">Секунд</span></span></span></div>');
		let visibleDay = $visible.find('.countdown-amount').eq(0);
		let visibleDayText = $visible.find('.countdown-period').eq(0);
		let visibleHour = $visible.find('.countdown-amount').eq(1);
		let visibleHourText = $visible.find('.countdown-period').eq(1);
		let visibleMinute = $visible.find('.countdown-amount').eq(2);
		let visibleMinuteText = $visible.find('.countdown-period').eq(2);
		let visibleSecond = $visible.find('.countdown-amount').eq(3);
		let visibleSecondText = $visible.find('.countdown-period').eq(3);

		$visible.insertAfter(this.$container);

		this.$container.countdown({
			until: `+${time[0]}d +${time[1]}h +${time[2]}m +${time[3]}s`,
			format: 'DHMS',
			padZeroes: true,
			onTick: updateCountdown,
			tickInterval: 1
		});

		function updateCountdown(period) {
			visibleDay.text(setTwoDigits(period[3]));
			visibleDayText.text($('.js-countdown').find('.countdown-period').eq(0).text());
			visibleHour.text(setTwoDigits(period[4]));
			visibleHourText.text($('.js-countdown').find('.countdown-period').eq(1).text());
			visibleMinute.text(setTwoDigits(period[5]));
			visibleMinuteText.text($('.js-countdown').find('.countdown-period').eq(2).text());
			visibleSecond.text(setTwoDigits(period[6]));
			visibleSecondText.text($('.js-countdown').find('.countdown-period').eq(3).text());
		}

		function setTwoDigits(n) {
			return n > 9 ? '' + n : '0' + n;
		}

		function msToTime(duration) {
			let seconds = parseInt((duration) % 60);
			let minutes = parseInt((duration / 60) % 60);
			let hours = parseInt((duration / 60 / 60) % 24);
			let days = parseInt((duration / 60 / 60 / 24));

			days = (days < 10) ? '0' + days : days;
			hours = (hours < 10) ? '0' + hours : hours;
			minutes = (minutes < 10) ? '0' + minutes : minutes;
			seconds = (seconds < 10) ? '0' + seconds : seconds;

			return days + ':' + hours + ':' + minutes + ':' + seconds;
		}
	}

}

export default new Countdown();
