'use strict';

/**
 * App entry point.
 *
 * @module App
 */

import 'babel-polyfill';

/** Import common components (initialized by default) */
import './components/common';


/** Import page controllers */
import Main from './pages/main/main.page';
import Results from './pages/results/results.page';
import Price from './pages/price/price.page';
import Qa from './pages/qa/qa.page';
import Calculator from './pages/calculator/calculator.page';
import About from './pages/about/about.page';
import FirstEntry from './pages/first-entry/first-entry.page';
import InfoPage from './pages/info/info.page';
import NutritionPage from './pages/nutrition/nutrition.page';
import SettingsPage from './pages/settings/settings.page';
import PersonalPage from './pages/personal/personal.page';
import { FatPage, FatTable } from './pages/fat/fat.page';
import { ProgramPage, ProgramTable } from './pages/program/program.page';

/** Import utils */
import { currentPage } from './modules/dev/helpers';

/**
 * Run appropriate scripts for each page.
 **/
switch (currentPage) {
  /** Main page */
  case 'main': {
    Main.init();
    break;
  }
	/** Results page */
	case 'results': {
		Results.init();
		break;
	}
	/** Price page */
	case 'price': {
		Price.init();
		break;
	}
	/** Qa page */
	case 'qa': {
		Qa.init();
		break;
	}
	/** Qa page */
	case 'calculator': {
		Calculator.init();
		break;
	}
	/** About page */
	case 'about': {
		About.init();
		break;
	}
	/** First Entry page */
	case 'first-entry': {
		let initFirstEntry = new FirstEntry();
		
		window.PublicAPI.FirstEntry = {
			setResult: (result) => {
				initFirstEntry.setResult(result);
			},
			removeResult: () => {
				initFirstEntry.removeResult();
			}
		};
		break;
	}
	/** Info page */
	case 'info': {
		InfoPage.init();
		break;
	}
	/** Nutrition page */
	case 'nutrition': {
		NutritionPage.init();
		break;
	}
	/** Settings page */
	case 'settings': {
		SettingsPage.init();
		break;
	}
	/** Personal Data page */
	case 'personal-data': {
		PersonalPage.init();
		window.PublicAPI.FirstEntry = {
			setResult: (result) => {
				PersonalPage.setResult(result);
			},
			removeResult: () => {
				PersonalPage.removeResult();
			}
		};
		break;
	}
	/** Fat page */
	case 'fat': {
		(new FatPage()).init();
		(new FatTable()).init();
		break;
	}
	case 'program': {
		(new ProgramPage()).init();
		(new ProgramTable()).init();
	}
}
import './components/videoController';
