import ScrollMagic from 'ScrollMagic';
import {TimelineMax} from 'gsap';
import {Resp} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class HowLooks {

	constructor() {
		this.$container = $('.how-looks');
		this.$desc = this.$container.find('.how-looks__desc');
		this.$title = this.$container.find('.how-looks__title');
		this.$videoContainer = this.$container.find('.how-looks__video');
		this.$video = this.$videoContainer.find('video');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$desc);
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addInnerWrap(this.$videoContainer);
	}

	_createAnimation() {
		this.$video.css({
			opacity: 0
		});

		this.tl
			.addLabel('start')

			//desc animation
			.from(this.$desc.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$desc, 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'start')
			.addLabel('descEnd')

			//title animation
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'descEnd-=0.75')
			.from(this.$title, 1, {
				x: '-75%',
				ease: Power3.easeOut,
			}, 'descEnd-=0.75')
			.addLabel('titleEnd')

			//videoContainer animation
			.from(this.$videoContainer.children(), 2, {
				x: '-115%',
				ease: Power4.easeInOut,
			}, 'titleEnd-=1.5')
			.from(this.$videoContainer, 2, {
				x: '-25%',
				ease: Power4.easeInOut,
			}, 'titleEnd-=1.5')
			.addLabel('videoEnd');
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();

				//play video after animation
				this.tl.vars.onComplete = () => {
					//check if video exists
					if (this.$video.length) {
						this.$video.css({
							opacity: 1
						});
						this.$videoContainer.find('.how-looks__video-image').fadeOut(1000);
						this.$video.get(0).play();
					}
				};
			}
		});
	}

}

export const Main06 = new HowLooks();
