import { $header, Resp } from '../../../modules/dev/helpers';
import {TimelineMax} from 'gsap';
import WrapHidden from '../../../modules/dev/wrapHidden';

export default class MainScreen {

	constructor() {
		this.$header = $header;
		this.$logo = this.$header.find('.header__logo');
		this.$controlsBtn = this.$header.find('.header__controls-btn').children();
		this.$container = $('.main-screen');
		this.$hoverItem = this.$container.find('.main-screen__right-item');
		this.$videoContainer = this.$container.find('.main-screen__right-video');
		this.$video = this.$videoContainer.find('video');
		this.$videoText = this.$container.find('.main-screen__right-text');
		this.$titleLarge = this.$container.find('.main-screen__title-lg');
		this.$subtitleLarge = this.$container.find('.main-screen__subtitle-lg');
		this.$title = this.$container.find('.main-screen__title');
		this.$subtitle = this.$container.find('.main-screen__subtitle');
		this.$btn = this.$container.find('.main-screen__btn');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._setAnimation();
		} else {
			this.$videoContainer.each((index, el) => {
				if ($(el).find('video').length) {
					//hide image
					if ($(el).find('.main-screen__right-image').length) {
						$(el).find('.main-screen__right-image').hide();
					}
				}
			});
		}
		this._bindHoverEvent();
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$logo);
		WrapHidden.addInnerWrap(this.$titleLarge);
		WrapHidden.addInnerWrap(this.$subtitleLarge);
		WrapHidden.addWrap(this.$btn, false);
		WrapHidden.addWrap(this.$controlsBtn);
	}

	//TODO: refactor this piece of shit
	_setAnimation() {
		this.$videoContainer.each((index, el) => {
			if ($(el).find('video').length) {
				$(el).find('video').hide();
			}
		});

		this.tl
			.addLabel('start')
			.staggerFrom(this.$videoContainer, 1, {
				y: function (index, target) {
					return ($(target).parent().index() % 2 === 0 ? -100 : 100) + '%';
				},
				ease: Power3.easeOut,
			}, null)
			.addLabel('videoEnd')
			.from(this.$videoText, 0.25, {
				autoAlpha: 0,
				ease: Power3.easeOut,
				clearProps: 'all'
			}, 'videoEnd-=0.5')
			.from(this.$logo.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$logo, 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'start')
			.addLabel('logoEnd')
			.from(this.$subtitleLarge.children(), 1.25, {
				x: '-115%',
				ease: Power3.easeInOut
			}, 'logoEnd-=1.25')
			.from(this.$subtitleLarge, 1.25, {
				x: '-25%',
				ease: Power3.easeInOut
			}, 'logoEnd-=1.25')
			.addLabel('subtitleLargeEnd')
			.from(this.$titleLarge.children(), 2, {
				x: '-115%',
				ease: Power3.easeInOut
			}, 'logoEnd-=1.25')
			.from(this.$titleLarge, 2, {
				x: '-25%',
				ease: Power3.easeInOut
			}, 'logoEnd-=1.25')
			.staggerFrom(this.$title.children().children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 0.1, 'subtitleLargeEnd-=0.25')
			.staggerFrom(this.$title.children(), 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 0.1, 'subtitleLargeEnd-=0.25')
			.addLabel('titleEnd')
			.staggerFrom(this.$subtitle.children().children(), 0.75, {
				x: '-115%',
				ease: Power3.easeOut
			}, 0.1, 'titleEnd-=0.25')
			.staggerFrom(this.$subtitle.children(), 0.75, {
				x: '-75%',
				ease: Power3.easeOut
			}, 0.1, 'titleEnd-=0.25')
			.addLabel('subtitleEnd')
			.from(this.$btn, 0.75, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'subtitleEnd-=0.25')
			.from(this.$btn.parent(), 0.75, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'subtitleEnd-=0.25')
			.staggerFrom(this.$controlsBtn, 0.75, {
				x: '115%',
				ease: Power3.easeOut
			}, 0.15, 'subtitleLargeEnd-=0.25')
			.staggerFrom(this.$controlsBtn.parent(), 0.75, {
				x: '75%',
				ease: Power3.easeOut
			}, 0.15, 'subtitleLargeEnd-=0.25')
			.set(this.$title.children(), {
				clearProps: 'overflow'
			})
			.set(this.$subtitle.children(), {
				clearProps: 'overflow'
			});

		this.tl.vars.onComplete = () => {
			this.$videoContainer.each((index, el) => {
				if ($(el).find('video').length) {
					//hide image
					if ($(el).find('.main-screen__right-image').length) {
						$(el).find('video').show();
						$(el).find('.main-screen__right-image').fadeOut(1000);
					}
				}
			});
		};
	}

	playAnimation() {
		if (Resp.isDesk) {
			this.tl.play();
		}
	}

	_bindHoverEvent() {
		let timeout;

		//play video on hover
		this.$hoverItem.on('mouseenter', (ev) => {
			//play video after CSS animation
			timeout = setTimeout(() => this.$video[this.$hoverItem.index(ev.currentTarget)].play(), 500);
		});

		//pause video on leave
		this.$hoverItem.on('mouseleave', (ev) => {
			clearTimeout(timeout);
			this.$video[this.$hoverItem.index(ev.currentTarget)].pause();
		});
	}
}

export const Main01 = new MainScreen();
