import {TimelineMax} from 'gsap';
import {Resp} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class StartNow {

	constructor() {
		this.$container = $('.start-now');
		this.$title = this.$container.find('.start-now__title');
		this.$subtitle = this.$container.find('.start-now__subtitle');
		this.$timer = this.$container.find('.start-now__timer');
		this.tl = new TimelineMax({ paused: true });
	}

	_createAnimation() {
		this.tl
			.addLabel('start')

			//title
			.from(this.$title, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$title.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'start')
			.addLabel('titleEnd')

			//subtitle
			.from(this.$subtitle, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'titleEnd-=0.5')
			.from(this.$subtitle.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'titleEnd-=0.5')
			.addLabel('subtitleEnd')

			//timer
			.from(this.$timer.next(), 1, {
				autoAlpha: 0,
				ease: Power3.easeOut
			}, 'subtitleEnd-=0.5')
			.addLabel('timerEnd')

			//timer text
			.set(this.$timer.next(), {
				css: {
					className: '+=is-animate'
				}
			}, 'timerEnd-=0.5');
	}

	_wrapHidden() {
		WrapHidden.addWrapClone(this.$title);
		WrapHidden.addWrapClone(this.$subtitle, { setWidthAuto: true, marginAuto: true });
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

}

export const Main11 = new StartNow();
