import { changeText } from '../modules/dev/helpers';

class ChangeText {
	constructor() {
		this.$el = $('.js-change-text');
	}

	bindEvents() {
		this.$el.on('click tap', (e) => {
			e.preventDefault();
			changeText(e.currentTarget);
		});
	}

	init() {
		this.bindEvents();
	}

}

export default new ChangeText().init();
