;(function () {
	if ($('#alphaVideo').length && $('.trainer').find('canvas').length) {
		//wait video loads
		$('#alphaVideo').bind('loadedmetadata', () => {
			let outputCanvas = document.getElementById('outputAlphaVideo'),
				output = outputCanvas.getContext('2d'),
				bufferCanvas = document.getElementById('bufferAlphaVideo'),
				buffer = bufferCanvas.getContext('2d'),
				video = document.getElementById('alphaVideo'),
				width = $(video).innerWidth(),
				height = $(video).innerHeight(),
				interval;

			$(outputCanvas)
				.attr({
					width: width,
					height: height
				});
			$(bufferCanvas)
				.attr({
					width: width,
					height: height
				});

			$(bufferCanvas).hide();
			$(video).hide();

			function processFrame() {
				buffer.drawImage(video, 0, 0, width, height);

				// this can be done without alphaData, except in Firefox which doesn't like it when image is bigger than the canvas
				let image = buffer.getImageData(0, 0, width, height),
					imageData = image.data,
					alphaData = buffer.getImageData(0, 0, width, height).data;

				for (let i = 3, len = imageData.length; i < len; i = i + 4) {
					imageData[i] = alphaData[i - 1];
				}

				output.putImageData(image, 0, 0, 0, 0, width, height);
			}

			video.addEventListener('play', function () {
				clearInterval(interval);
				interval = setInterval(processFrame, 25);
			}, false);

			// Firefox doesn't support looping video, so we emulate it this way
			video.addEventListener('ended', function () {
				video.play();
			}, false);

			//TODO: play video only when it's in View
			// setTimeout(function () {
			// 	video.pause();
			// }, 7000);
		});
	}
})();
