import { TimelineMax, CSSPlugin, TweenMax } from 'gsap';
import { Resp } from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class ProgramPower {

	constructor() {
		this.$container = $('.program-power');
		this.$box = this.$container.find('.program-power__list-box');
		this.$title = this.$container.find('.program-power__title');
		this.$icon = this.$container.find('.program-power__list-icon');
		this.$textTitle = this.$container.find('h4');
		this.$text = this.$container.find('p');
		this.tl = new TimelineMax({ paused: true });
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addWrapClone(this.$icon);
		WrapHidden.addWrapClone(this.$text);

		if (this.$textTitle.length) {
			WrapHidden.addWrapClone(this.$textTitle, { setWidthAuto: true, marginAuto: true });
		}
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

	_createAnimation() {
		//set box height equal to container
		TweenMax.set(this.$box, {
			height: this.$container.outerHeight() + 20
		});
		TweenMax.set(this.$box, {
			'margin-top': this.$container.offset().top - this.$box.offset().top
		});

		//align center icons
		TweenMax.set(this.$icon.parent(), {
			'margin-left': 'auto',
			'margin-right': 'auto'
		});

		//align center text
		TweenMax.set(this.$text.parent(), {
			'margin-left': 'auto',
			'margin-right': 'auto'
		});

		this.tl
			.addLabel('start')

			//box animation
			.staggerTo(this.$box, 1.5, {
				y: function (index, target) {
					return ($(target).parent().index() % 2 === 0 ? 55 : -155) + '%';
				},
				ease: Power3.easeInOut,
				delay: 0.25,
			}, null, 'start')
			.addLabel('boxEnd')

			//title animation
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'boxEnd-=1')
			.from(this.$title, 2, {
				x: '-25%',
				ease: Power3.easeOut
			}, 'boxEnd-=1')
			.addLabel('titleEnd')

			//icon animation
			.from(this.$icon, 1, {
				x: '-150%',
				ease: Power3.easeOut
			}, 'titleEnd-=1')
			.from(this.$icon.parent(), 1, {
				x: '-25%',
				ease: Power3.easeOut
			}, 'titleEnd-=1')
			.addLabel('iconEnd')

			//text animation
			.from(this.$text, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, this.$textTitle.length ? 'iconEnd+=0.25' : 'iconEnd-=0.35')
			.from(this.$text.parent(), 1, {
				x: '-25%',
				ease: Power3.easeOut
			}, this.$textTitle.length ? 'iconEnd+=0.25' : 'iconEnd-=0.35')
			.addLabel('textEnd');

		if (this.$textTitle.length) {
			this.tl
				.add(
					[
						TweenMax.from(this.$textTitle, 1, {
							x: '-115%',
							ease: Power3.easeOut
						}),
						TweenMax.from(this.$textTitle.parent(), 1, {
							x: '-25%',
							ease: Power3.easeOut
						})
					], 'titleEnd-=0.35'
				);
		}
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

}

export const Main08 = new ProgramPower();
