/**
 * About page scripts.
 *
 * @module About
 */

/** Import sections scripts */
import WaitPreloader from '../../components/waitPreloader';
import { About01 } from './sections/01_trainer';
import { About02 } from './sections/02_program-power';
import { About03 } from './sections/03_helpers';
import { About04 } from './sections/04_program-steps';
import { Resp } from '../../modules/dev/helpers';

/** Class representing a website's About page functions. Initialized only on web site's about page. */
export default class About {
	/**
	 * Initialize About page scripts.
	 *
	 * @static
	 */
	static init() {
		WaitPreloader.init().then(() => {
			if (Resp.isDesk) {
				About01._play();
			}
		});
		About01._alphaVideoInView();
		if (Resp.isDesk) {
			About01._wrapHidden();
			About01._createAnimation();
		}
		About02.init();
		About03.init();
		About04.init();
	}
}
