import {TimelineMax, CSSPlugin} from 'gsap';
import { Resp, css, currentPage } from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class ProgramSteps {

	constructor() {
		this.$container = $('.program-steps');
		this.$item = this.$container.find('.program-steps__list-item');
		this.$title = this.$container.find('.program-steps__list-text');
		this.$text = this.$container.find('p');
		this.startTl = new TimelineMax({ paused: true });
		this.tl = new TimelineMax({ paused: true });
		this.tlReverse = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();

			// safari fix
			TweenMax.set(this.$title.parent(), {
				clearProps: 'overflow'
			});
			this._playOnScroll();
		}

		if (Resp.isTablet) {
			this._wrapHidden();
			this._setActiveClassItem(0);
			this._createAnimation(0);
			this.tl.play();
			let ind = false;
			this.tl.vars.onComplete = () => {
				if (!ind) {
					ind = true;

					//bind events after animations
					this._bindHoverEvent();
				}
			};
		}

		if (Resp.isMobile) {
			this._createMobileNav();
			this._initMobile();
		}
	}

	_createMobileNav() {
		let $navItem = $('<div class="program-steps__nav-item"></div>');
		this.$container.find('.program-steps__list-item').each((indexContainer) => {
			let $nav = $('<div class="program-steps__nav"></div>');
			$nav.clone().appendTo(this.$container.find('.program-steps__list-item').eq(indexContainer).find('.program-steps__list-center'));
			this.$title.each((index, el) => {
				$navItem.text($(el).data('title'));
				$navItem.clone().appendTo(this.$container.find('.program-steps__list-item').eq(indexContainer).find('.program-steps__nav'));
			});
		});

		//set active first el
		this.$container.find('.program-steps__list-item').eq(0).find('.program-steps__nav-item').eq(0).addClass(css.active);
	}

	_initMobile() {
		let btn = this.$container.find('.program-steps__nav-item');

		btn.on('click tap', (ev) => {
			btn.removeClass(css.active);
			this.$item.eq($(ev.currentTarget).index()).find(btn).eq($(ev.currentTarget).index()).addClass(css.active);

			//hide all tabs
			$(ev.currentTarget).closest(this.$item).fadeOut();

			//show current tab
			this.$item.eq($(ev.currentTarget).index()).fadeIn();
		});
	}

	_playOnScroll() {
		this._startAnimation();
		this._createAnimation(0);
		this._createReverseAnimation();

		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				//init ind
				let ind = false;
				this.startTl.play();

				// wait complete first start animation
				setTimeout(() => {
					this.$item.addClass('has-transition');
					this._setActiveClassItem(0);
					this.tl.play();

					this.tl.vars.onComplete = () => {
						if (!ind) {
							ind = true;

							//bind events after animations
							this._bindHoverEvent();
						}
					};
				}, 1000 * this.startTl._duration);
			}
		});
	}

	_setActiveClassItem(index) {
		this.$item.eq(index).addClass(css.active).siblings().removeClass(css.active);
	}

	_startAnimation() {
		this.startTl
			.addLabel('start')

			//title
			.from(this.$container.find('.program-steps__title'), 1, {
				x: '-115%',
				ease: Power2.easeOut,
				clearProps: 'transform'
			}, 'start')
			.from(this.$container.find('.program-steps__title').parent(), 1, {
				x: '-75%',
				ease: Power2.easeOut,
				clearProps: 'overflow, transform'
			}, 'start')
			.addLabel('titleEnd')

			//item
			.staggerFrom(this.$item, 0.75, {
				alpha: 0,
				x: function (index, target) {
					return ($(target).index() > 0) ? 200 : -200;
				},
				ease: Power3.easeOut
			}, 0.35, 'titleEnd-=1');
	}

	_wrapHidden() {
		WrapHidden.addWrapClone(this.$container.find('.program-steps__title'), { setWidthAuto: true, marginAuto: true });
		WrapHidden.addWrapClone(this.$container.find('.program-steps__list-text'), { setWidthAuto: true, marginAuto: true });
		WrapHidden.addWrapClone(this.$container.find('p'), { setWidthAuto: true, marginAuto: true });
	}

	_setAnimation(el) {
		el
			.addLabel('start')

		//title
			.to(this.$title, 0.5, {
				x: '-150%',
				ease: Power3.easeInOut
			}, 'start')
			.set(this.$center, {
				clearProps: 'padding-left'
			}, 'start')
			.set(this.$center, {
				css: {
					paddingLeft: Resp.isTablet ? '60px' : '55px',
					paddingRight: '20px'
				}
			})
			.addLabel('titleEnd')

			.set(this.$title, {
				x: '-130%',
				onComplete: function () {
					$(this.target).addClass(css.active);
					TweenMax.set($(this.target), {
						css: {
							position: 'relative',
							top: 0,
							left: 0
						},
						immediateRender: false
					});
				},
				onReverseComplete: function () {
					$(this.target).removeClass(css.active);
					TweenMax.set($(this.target), {
						clearProps: 'position, top, left'
					});
				}
			}, 'titleEnd')
			.set(this.$title.parent(), {
				x: '-25%'
			}, 'titleEnd')
			.addLabel('setTitleEnd')

			.to(this.$title, 0.75, {
				x: '0%',
				ease: Power3.easeInOut
			}, 'setTitleEnd')
			.to(this.$title.parent(), 0.75, {
				x: '0%',
				ease: Power3.easeInOut
			}, 'setTitleEnd')
			.addLabel('titleFullEnd')

			.from(this.$subtitle, 0.75, {
				x: '-115%',
				ease: Power3.easeInOut,
				clearProps: 'transform'
			}, 'titleFullEnd-=0.5')
			.from(this.$subtitle.parent(), 0.75, {
				x: '-25%',
				ease: Power3.easeInOut,
				clearProps: 'transform'
			}, 'titleFullEnd-=0.5')
			.addLabel('subtitleEnd');
	}

	_createAnimation(index) {
		this.$title = this.$item.eq(index).find('.program-steps__list-text');
		this.$subtitle = this.$item.eq(index).find('p');
		this.$center = this.$item.eq(index).find('.program-steps__list-center');

		TweenMax.set(this.$center, { clearProps: 'paddingLeft' });
		TweenMax.set(this.$title, { clearProps: 'transform' });
		TweenMax.set(this.$title.parent(), { clearProps: 'overflow' });
		TweenMax.set(this.$subtitle, { clearProps: 'transform' });
		TweenMax.set(this.$subtitle.parent(), { clearProps: 'transform' });

		//clear timeline
		this.tl.clear();

		//set timeline
		this._setAnimation(this.tl);

		//show subtitle
		this.tl
			.add(() => {
				new TimelineMax()
					.set(this.$subtitle.parent(), {
						css: {
							display: 'block'
						}
					})
					.set(this.$subtitle, {
						css: {
							display: 'block'
						}
					});
			}, 'titleFullEnd-=0.5');
	}

	_createReverseAnimation() {
		let _title = this.$title;
		let _titleParent = this.$title.parent();
		let _subtitle = this.$subtitle;
		let _subtitleParent = this.$subtitle.parent();

		this.tlReverse.clear();

		//set reversed timeline
		this._setAnimation(this.tlReverse);

		//hide subtitle
		this.tlReverse
			.add(() => {
				new TimelineMax()
					.set(_subtitle.parent(), {
						css: {
							display: 'none'
						}
					})
					.set(_subtitle, {
						css: {
							display: 'none'
						}
					});
			}, 'setTitleEnd');

		this.tlReverse.vars.onReverseComplete = () => {
			TweenMax.set(_title, { clearProps: 'transform' });
			TweenMax.set(_titleParent, { clearProps: 'overflow' });
			TweenMax.set(_subtitle, { clearProps: 'transform' });
			TweenMax.set(_subtitleParent, { clearProps: 'transform' });
		};
	}

	_bindHoverEvent() {
		this.$item.on('mouseenter', (ev) => {
			if (!$(ev.currentTarget).hasClass(css.active)) {

				this.tlReverse.progress(0, false);
				this.tl.progress(1, false);

				//add active class
				this._setActiveClassItem($(ev.currentTarget).index());

				//reverse animation
				this._createReverseAnimation();
				this.tlReverse.timeScale(5).reverse(0);

				//show tab with animation
				this._createAnimation($(ev.currentTarget).index());

				//play animation for current tab
				this.tl.play();

			}
		});
	}

}

export const Main05 = new ProgramSteps();
