import ScrollMagic from 'ScrollMagic';
import {TimelineMax} from 'gsap';
import {Resp, $window, css} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class Target {

	constructor() {
		this.$container = $('.target');
		this.$tabNav = this.$container.find('.target__left-tab');
		this.$hoverItem = this.$container.find('.target__right-item');
		this.$videoContainer = this.$container.find('.target__right-video');
		this.$video = this.$videoContainer.find('video');
		this.$videoText = this.$container.find('.target__right-text');
		this.$titleLarge = this.$container.find('.target__title-lg');
		this.startTl = new TimelineMax({ paused: true });
		this.tl = new TimelineMax({ paused: true });
		this.$desc = this.$container.find('.target__desc');
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._playOnScroll();
		} else {
			this.$videoContainer.each((index, el) => {
				if ($(el).find('video').length) {
					//hide image
					if ($(el).find('.target__right-image').length) {
						$(el).find('.target__right-image').hide();
					}
				}
			});
		}

		if (Resp.isTablet) {
			this._wrapHidden();
			this._bindHoverEvent();
		}

		if (Resp.isMobile) {
			this._wrapHidden();
			this._bindHoverEvent('mobile');
		}

	}

	_playOnScroll() {
		this._startAnimation();
		this._createAnimation(0);

		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				//init ind
				let ind = false;
				this.startTl.play();

				//wait complete first start animation
				setTimeout(() => {
					this.tl.play();

					this.tl.vars.onComplete = () => {
						if (!ind) {
							ind = true;
							this._setActiveClassItem(0);

							//bind events after animations
							this._bindHoverEvent();

							//play first video if exists after all animations
							if (this.$hoverItem.eq(0).find('video').length) {
								this.$hoverItem.eq(0).find('video').get(0).play();
							}
						}
					};
				}, 1000 * (this.startTl._duration - 0.5));
			}
		});
	}

	_setActiveClassItem(index) {
		this.$hoverItem.eq(index).addClass(css.active).siblings().removeClass(css.active);
	}

	_startAnimation() {
		this.$videoContainer.each((index, el) => {
			if ($(el).find('video').length) {
				$(el).find('video').hide();
			}
		});

		this.startTl
			.addLabel('start')
			.staggerFrom(this.$videoContainer, 1, {
				y: function (index, target) {
					return ($(target).parent().index() % 2 === 0 ? -100 : 100) + '%';
				},
				ease: Power3.easeOut,
			}, null, 'start')
			.addLabel('videoEnd')
			.from(this.$videoText, 0.25, {
				autoAlpha: 0,
				ease: Power3.easeOut,
				clearProps: 'all'
			}, 'videoEnd-=0.5')
			.from(this.$titleLarge.children(), 1.25, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'videoEnd-=1.25')
			.from(this.$titleLarge, 2, {
				x: '-25%',
				ease: Power3.easeOut
			}, 'videoEnd-=1.25')
			.addLabel('titleLargeEnd')
			.from(this.$desc.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'titleLargeEnd-=1')
			.from(this.$desc, 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'titleLargeEnd-=1');

		this.startTl.vars.onComplete = () => {
			this.$videoContainer.each((index, el) => {
				if ($(el).find('video').length) {
					//hide image
					if ($(el).find('.target__right-image').length) {
						$(el).find('video').show();
						$(el).find('.target__right-image').fadeOut(1000);
					}
				}
			});
		};
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$titleLarge);
		WrapHidden.addInnerWrap(this.$desc);
		WrapHidden.addInnerWrap(this.$container.find('.target__title'));
		WrapHidden.addWrap(this.$container.find('ul'));
		WrapHidden.addWrap(this.$container.find('p:last-child'));
	}

	_createAnimation(index) {
		this.$title = this.$tabNav.eq(index).find('.target__title');
		this.$ul = this.$tabNav.eq(index).find('ul');
		this.$p = this.$title.siblings().last().children('p');

		//clear transform
		TweenMax.set(this.$title.children(), { clearProps: 'transform' });
		TweenMax.set(this.$title, { clearProps: 'transform' });
		TweenMax.set(this.$ul, { clearProps: 'transform' });
		TweenMax.set(this.$ul.parent(), { clearProps: 'transform' });
		TweenMax.set(this.$p, { clearProps: 'transform' });
		TweenMax.set(this.$p.parent(), { clearProps: 'transform' });

		//clear timeline
		this.tl.clear();

		//set timeline
		this.tl
			.addLabel('start')
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'start')
			.from(this.$title, 1, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'start')
			.addLabel('titleEnd')
			.from(this.$ul, this.$ul.length ? 1 : null, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'titleEnd-=0.625')
			.from(this.$ul.parent(), this.$ul.length ? 1 : null, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'titleEnd-=0.625')
			.addLabel('ulEnd')
			.from(this.$p, 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'ulEnd-=0.625')
			.from(this.$p.parent(), 1, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'ulEnd-=0.625')
			.addLabel('pEnd');
	}

	/**
	 *
	 * @private
	 * @param {Object} mobile
	 */
	_bindHoverEvent(mobile = {}) {
		let timeout,
			timer,
			timerDelay = 0;

		if (mobile === 'mobile') {
			this._setActiveClassItem(0);
		}

		//play video on hover
		this.$hoverItem.on('mouseenter', (ev) => {
			if ($(ev.currentTarget).hasClass(css.active)) return;

			//throttle mouse hover event
			timer = setTimeout(() => {
				//add active class
				this._setActiveClassItem($(ev.currentTarget).index());

				//pause videos
				this.$video.each(function () {
					if (!$(this).get(0).paused) {
						$(this).get(0).pause();
					}
				});

				//play video after CSS animation
				timeout = setTimeout(() => {
					if ($(ev.currentTarget).find('video').length) {
						$(ev.currentTarget).find('video').get(0).play();
					}
				}, 500);

				//reverse animation
				this.tl.timeScale(3).reverse();

				this.tl.vars.onReverseComplete = () => {
					this.tl.timeScale(2);
					this.$tabNav.hide();

					//show tab with animation
					this._createAnimation($(ev.currentTarget).index());

					//show tab with hidden elements
					this.$tabNav.eq($(ev.currentTarget).index()).show();

					//play animation for current tab
					this.tl.timeScale(2).play();
				};
			}, timerDelay);
		});

		//pause video on leave
		this.$hoverItem.on('mouseleave', (ev) => {
			clearTimeout(timer);
		});
	}

}

export const Main04 = new Target();
