import {TimelineMax} from 'gsap';
import {Resp} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class Teaser1 {

	constructor() {
		this.$container = $('.main-teaser-1');
		this.$title = this.$container.find('.main-teaser-1__title');
		this.$btn = this.$container.find('.main-teaser-1__btn');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addWrap(this.$btn);
	}

	_createAnimation() {
		this.tl
			.addLabel('start')

			//title animation
			.from(this.$title.children(), 1.5, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$title, 1.5, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'start')
			.addLabel('titleEnd')

			//button animation
			.from(this.$btn, 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'titleEnd-=0.5')
			.from(this.$btn.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
			}, 'titleEnd-=0.5')
			.addLabel('btnEnd');
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

}

export const Main07 = new Teaser1();
