/* eslint-disable semi,no-trailing-spaces,indent,no-undef,space-before-function-paren */
import { TimelineMax } from 'gsap';
import { $window, css, Resp } from '../../modules/dev/helpers';
import Fame from '../main/sections/10_fame';
import './../../modules/dep/DrawSVGPlugin';

export class FatPage {
	constructor() {
		this.$dotEl = $('.fat-eat__recipe h4');
		this.$recipe = $('.fat-eat__recipe');
		this.$recipeDownload = $('.fat-eat').find('.fat__controls-download');
	}
	
	init() {
		this.dot();
    this.initGirl();
		
		if (Resp.isMobile) {
			this.replaceRecipeDownload();
		}
	}
	
	dot() {
		$window.on('load', () => {
			this.$dotEl.dotdotdot();
		});
	}

  initGirl() {
    const $girl = $('.girl');
    const $svg = $girl.find('.girl__svg');
    const $girlBtn = $girl.find('.girl__circle:not(.girl__circle-full)');
    const $object = $svg.find('.girl__svg-objects').children();
    const $mainBtn = $girl.find('.girl__circle-full');
    const $mainCircle = $svg.find('.girl__svg-main-circle');
    let tlMain = new TimelineMax({ paused: true });
    const colors = {
      light: '#F8F8F8',
      disabled: '#9ea5af',
      dark: '#63676c'
    };

    setObjectsColors();
    setMainObjectColor();
    createAnim({
      timeline: tlMain,
      circle: $mainCircle
    });

    TweenMax.set($mainCircle, { scale: 0 });

    $mainBtn.on('mouseenter', () => {
      tlMain.play();
    });

    $mainBtn.on('mouseleave', () => {
      tlMain.reverse();
    });

    $girlBtn.each((index, el) => {
      let $this = $(el);
      let $line = $this.find('svg polyline').eq(1);
      let $circle = $svg.find('defs clipPath').eq(index).find('circle');
      $this.animation = new TimelineMax({ paused: true });
      TweenMax.set($line, { drawSVG: 0 });
      TweenMax.set($circle.get(0), { scale: 0 });
      createAnim({ timeline: $this.animation, line: $line, circle: $circle });

      $this.on('mouseenter', () => {
        $this.animation.play();
      });

      $this.on('mouseleave', () => {
        $this.animation.reverse();
      });
    });

    /**
     * @param {Object} parameters - { timeline, line, circle }
     *
     * timeline - gsap timeline
     * line - jQuery line element
     * circle - jQuery circle element
     */
    function createAnim(parameters) {
      let { timeline, line, circle } = parameters;
      if (line === undefined) {
        timeline
          .to(circle, 1, {
            scale: 1,
            transformOrigin: '50% 50%',
            smoothOrigin: false,
            ease: Power4.ease
          });
      } else {
        timeline
          .to(line, 0.5, {
            drawSVG: true,
            ease: Power2.easeOut
          })
          .set(circle, {
            scale: 1,
            transformOrigin: '50% 50%',
            smoothOrigin: false,
            ease: Power4.ease
          }, 0);
      }
    }

    function setObjectsColors() {
      $object.each((index, el) => {
        let $this = $(el);
        let thisColor = $girlBtn.eq(index).data('color');
        let thisColorHex = colors[thisColor];

        $this.attr('fill', thisColorHex);
      });
    }

    function setMainObjectColor() {
      let thisColor = $mainBtn.data('color');
      let thisColorHex = colors[thisColor];

      $object.last().attr({
        fill: thisColorHex,
        stroke: thisColorHex
      });
    }
  }
	
	replaceRecipeDownload() {
		if (this.$recipe.length && this.$recipeDownload.length) {
			this.$recipeDownload.insertAfter(this.$recipe);
		}
	}
}

export class FatTable extends Fame {
	constructor() {
		super();
		this.$container = $('.fat-competition');
		this.$table = this.$container.find('.fame-table');
		this.$btn = this.$container.find('.fat-competition__btn');
		this.$slider = this.$container.find('.fat-competition__fame-table-row');
		this.$sliderNav = this.$container.find('.fat-competition__fame-table-nav');
	}
	
	init() {
		this._checkRows();
		this._createSlider();
	}
	
	_createSlider() {
		const navLength = this.$sliderNav.children().length;
		const $activeItem = this.$sliderNav.children().filter('.' + css.active);
		const activeIndex = $activeItem.length ? $activeItem.index() : false;

		this.$slider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			fade: true,
			arrows: false,
			speed: 400,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			asNavFor: this.$sliderNav
		});
		
		this.$sliderNav.slick({
			slidesToShow: navLength,
			slidesToScroll: 1,
			asNavFor: this.$slider,
			dots: false,
			arrows: false,
			swipe: false,
			draggable: false,
			cssEase: 'ease',
			centerMode: true,
			focusOnSelect: true
		});
		
		if (activeIndex !== false) {
			this.$slider.slick('slickGoTo', activeIndex, false);
		} else {
			this.$sliderNav.find('.slick-current').removeClass('slick-current');
		}
	}
	
}
