/**
 * Website's common scripts.
 *
 * @module Common
 */

import objectFitImages from 'object-fit-images';
import objectFitVideos from 'object-fit-videos';
import FirstScreen from '../modules/dev/animation/firstSection';
import { $body, $window, css, Resp } from '../modules/dev/helpers';
import Split from './../modules/dev/split';
import WrapHidden from './../modules/dev/wrapHidden';
import MobileVideo from './autoMobVideo';
import './video';
import CTabs from './c-tabs';
import Countdown from './countdown';
import './crop';
import './fit';
import './initChangeText';
import Dot from './initDot';
import MainSocials from './main-socials';
import Menu from './menu';
import './noTouch';
import PageResize from './pageResize';
import './parallax';
import './perfectScroll';
import './popup';
import Preloader from './preloader';
import './publicAPI';
import { Selectric, select2 } from './selectInit';
import './slider';
import './to-top';
// static components
import './static/alphaVideo';
import './static/pageItem';
import './tooltip';
import Validate from './validate';
import './panel';

export class Common {
	init() {
		this.initPreloader();
		FirstScreen.init();
		objectFitVideos();
		objectFitImages();
		WrapHidden.init();
		Split.init();
		Menu.init();
		PageResize.init();
		MobileVideo.init();
		Countdown.init();
		MainSocials.init();
		Validate.init();
		Dot.init();
		CTabs.init();
		Selectric.init();
		select2.init();
  }
	
	initPreloader() {
		$window.on('load', () => {
			this.fixPanelText();
			this.fixFatText();
		});
		
		// animate preloaderOff
		if (!$('main').hasClass('js-prevent-preloaderOff') && !$('.js-first-screen').length) {
			Preloader.init();
		}
	}
	
	fixFatText() {
		if (Resp.isDesk) {
			const $el = $('.fat-stat__image-el p:first-child');
			const maxWidth = 120;
			
			$el.each((index, el) => {
				const $current = $(el);
				
				if (!$el.length) return;
				
				resizeText($current, maxWidth);
				
				function resizeText($currentEl, maxWidth) {
					if ($currentEl.width() > maxWidth) {
						$el.css('font-size', +($currentEl.css('font-size').slice(0, -2)) - 1 + 'px')
							.css('line-height', +($currentEl.css('line-height').slice(0, -2)) - 0.6 + 'px');
						
						resizeText($currentEl, maxWidth);
					}
				}
			});
		}
	}
	
	fixPanelText() {
		if (Resp.isDesk) {
			const $el = $('.panel__info-number');
			const maxWidth = 130;
			
			if (!$el.length) return;
			
			$el.each((index, el) => {
				const $current = $(el);
				
				resizeText($current, maxWidth);
				
				function resizeText($currentEl, maxWidth) {
					const $testDiv = $('<div></div>').css({
						position: 'fixed',
						'pointer-events': 'none',
						visibility: 'hidden',
						top: 0,
						left: 0,
						'white-space': 'nowrap',
						'font-size': $currentEl.css('font-size')
					});
					
					$testDiv.appendTo($body);
					$testDiv.append($currentEl.html());
					
					if ($testDiv.width() > maxWidth) {
						$el.css('font-size', +($currentEl.css('font-size').slice(0, -2)) - 1 + 'px')
							.css('line-height', +($currentEl.css('line-height').slice(0, -2)) - 0.6 + 'px');
						$testDiv.remove();
						
						resizeText($currentEl, maxWidth);
					}
					$testDiv.remove();
				}
			});
		}
	}
	
}

export default new Common().init();
