import 'croppie';
import Popup from 'vintage-popup/dist/vintage-popup.min';
import { css } from '../modules/dev/helpers';

export class Crop {
	constructor() {
		this.$btn = $('.js-upload-photo');
		this.$trigger = $('.js-trigger-upload');
		this.$uploadInput = $('#uploadImage');
		this.$savedImage = $('#savedImage');
		this.$stage1 = $('.modal__upload-stage-1');
		this.$stage2 = $('.modal__upload-stage-2');
		this.$saveBtn = $('.js-save-upload');
		this.$backBtn = $('.js-back-upload');
		this.$rotateBtn = $('.js-rotate-upload');
		this.cropTarget = null;
		this.uploadPopup = null;
		this.vertical = false;
	}
	
	init() {
		this.initInfoPopup();
		this.bindEvents();
		this.initUpload();
	}
	
	initInfoPopup() {
		// init modal
		Popup.expose($);
		this.$btn.popup({
			targetPopupId: 'uploadPhoto',
			closeOnEsc: false,
			closeBtnSelector: '.js-modal-close'
		});
		this.uploadPopup = $('[data-popup-id="uploadPhoto"]').data('popup');
	}
	
	bindEvents() {
		this.$btn.on('click tap', (ev) => {
			if (this.cropTarget !== $(ev.currentTarget).data('upload-target')) {
				this.$stage2.hide();
				this.$stage1.show();
			}
			this.cropTarget = $(ev.currentTarget).data('upload-target');
			
			if ($(ev.currentTarget).data('upload-vertical') === true) {
				this.uploadPopup.$popup.addClass('is-vertical');
			} else {
				this.uploadPopup.$popup.removeClass('is-vertical');
			}
		});
		
		this.$trigger.on('click tap', function (e) {
			e.preventDefault();
			$(this).siblings('input[type="file"]').trigger('click');
		});
	}
	
	initUpload() {
		let $uploadCrop;
		
		function readFile(input) {
			if (input.files && input.files[0]) {
				let reader = new FileReader();
				
				reader.onload = function (e) {
					$uploadCrop.croppie('bind', {
						url: e.target.result
					}).then(function () {
						//code here
					});
				};
				
				reader.readAsDataURL(input.files[0]);
			} else {
				console.error('Sorry - you\'re browser doesn\'t support the FileReader API');
			}
		}
		
		$uploadCrop = $('#uploadCrop').croppie({
			viewport: {
				type: 'square'
			},
			enableExif: true
		});
		
		this.$uploadInput.on('change', (ev) => {
			let $this = $(ev.currentTarget);
			
			if ($this.val()) {
				this.$stage1.fadeOut(200, () => {
					this.$stage2.fadeIn(200);
					readFile(ev.currentTarget);
				});
			}
		});
		
		this.$backBtn.on('click tap', (e) => {
			e.preventDefault();
			this.$uploadInput.val('');
			
			this.$stage2.fadeOut(200, () => {
				this.$stage1.fadeIn(200);
			});
		});
		
		this.$saveBtn.on('click tap', (e) => {
			e.preventDefault();
			$uploadCrop.croppie('result', {
				type: 'base64',
				size: 'viewport'
			}).then((resp) => {
				let $targetContainer = $('[data-upload-container="' + this.cropTarget + '"]');
				let prevImage = $targetContainer.find('img');
				let $image = $(`<img src="${resp}" alt="image">`);
				let $input = $targetContainer.find('input');
				
				if (prevImage.length) {
					prevImage.remove();
				}
				
				$input.val(resp).trigger('change');
				
				$targetContainer.addClass(css.image)
					.append($image.clone());
				
				setSettingsPageImage();
				
				this.$savedImage.val(resp);
				this.uploadPopup.close();
				this.$stage2.hide();
				this.$stage1.show();
				if (!$targetContainer.closest('form').hasClass('not-submit') &&
					!$targetContainer.hasClass('not-submit')) {
					this.$savedImage.closest('form').submit();
				}
				
				function setSettingsPageImage() {
					if ($targetContainer.hasClass('panel__image')) {
						if ($('.photo-upload').length) {
							$('.photo-upload__image').find('img').remove();
							$('.photo-upload__image').addClass(css.image).append($image.clone());
						}
					}
					
					if ($targetContainer.hasClass('photo-upload__image')) {
						if ($('.panel').length) {
							$('.panel__image').find('img').remove();
							$('.panel__image').addClass(css.image).append($image.clone());
						}
					}
				}
			});
		});

        this.$rotateBtn.on('click tap', function (e) {
            e.preventDefault();
            e.stopPropagation();

            var $imageToRotate = $('.cr-image');
            var rotate64 = function(base64data, degrees, enableURI) {
                return new Promise(function(resolve, reject) {
                    //assume 90 degrees if not provided
                    degrees = degrees ? degrees : 90;

                    var canvas = document.createElement("canvas");
                    canvas.setAttribute("id", "hidden-canvas");
                    canvas.style.display = "none";
                    document.body.appendChild(canvas);

                    var ctx = canvas.getContext("2d");

                    var image = new Image();
                    //assume png if not provided
                    image.src = (base64data.indexOf(",") == -1 ? "data:image/png;base64," : "") + base64data;
                    image.onload = function() {
                        var w = image.width;
                        var h = image.height;
                        var rads = degrees * Math.PI/180;
                        var c = Math.cos(rads);
                        var s = Math.sin(rads);
                        if (s < 0) { s = -s; }
                        if (c < 0) { c = -c; }
                        //use translated width and height for new canvas
                        canvas.width = h * s + w * c;
                        canvas.height = h * c + w * s;
                        //draw the rect in the center of the newly sized canvas
                        ctx.translate(canvas.width/2, canvas.height/2);
                        ctx.rotate(degrees * Math.PI / 180);
                        ctx.drawImage(image, -image.width/2, -image.height/2);
                        //assume plain base64 if not provided
                        resolve(enableURI ? canvas.toDataURL() : canvas.toDataURL().split(",")[1]);
                        $imageToRotate.attr('src', canvas.toDataURL());
                        document.body.removeChild(canvas);
                    };
                    image.onerror = function() {
                        reject("Unable to rotate data\n" + image.src);
                    };
                });
            }

            rotate64( $imageToRotate.attr('src'), 90 );

            // function doIt() {
            //     rotate64($imageToRotate.attr('src'))
            //         .then(function(rotated) {
            //             var img1 = document.createElement("img");
            //             img1.src = "data:image/png;base64," + data;
            //             img1.style.border = "1px solid blue";
            //             document.body.appendChild(img1);
            //
            //             var img2 = document.createElement("img");
            //             img2.src = "data:image/png;base64," + rotated;
            //             img2.style.border = "1px solid red";
            //             $imageToRotate.attr('src', img2)
            //             // document.body.appendChild(img2);
            //             console.log(rotated);
            //         }).catch(function(err) {
            //         console.error(err);
            //     });
            // }

        });

	}
}

export default (new Crop()).init();
