import { TweenMax, CSSPlugin } from 'gsap';
import { css, Resp, $scrolledElements } from '../../modules/dev/helpers';
import Popup from 'vintage-popup/dist/vintage-popup.min';

export default class FirstEntry {
	constructor() {
		this.$form = $('.st2-tabs').find('form.entry-1');
		this.$tabNav = $('.st2-tabs__tabs-nav').find('.st2-tabs__tabs-el');
		this.$tabItemContainer = $('.st2-tabs__tabs-for');
		this.$tabItem = this.$tabItemContainer.find('.st2-tabs__tab');
		this.$clearBtn = $('.photoload__card-close');
		this.ind = false;

		this.init();
	}

	init() {
		this.bindEvents();
	}

	bindEvents() {
		if (Resp.isMobile) {
			this.initMobile();
		}
		
		this.$clearBtn.on('click tap', function (e) {
			e.preventDefault();
			e.stopPropagation();
			$(this).siblings('img').remove();
			$(this).siblings('input[type=hidden]').val('').trigger('change');
			$(this).parent().removeClass(css.image);
		});
	}

	initMobile() {
		// dots
		let dots = `<div class="st2-tabs__dot st2-tabs__dot_1"></div><div class="st2-tabs__dot st2-tabs__dot_2"></div><div class="st2-tabs__dot st2-tabs__dot_3"></div>`;
		this.$tabNav.parent().append(dots);

		// prepare modal
		let mobMenu = `<div class="modal modal_modal" data-popup-id="menuPopup" tabindex="-1" role="dialog"><div class="modal__container"><div class="modal__close js-modal-close"><svg class="icon icon-close"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-close"></use></svg></div><div class="mobile-nav"><div class="mobile-nav__content"></div><div class="mobile-nav__btn"><button class="btn btn_red mobile-nav__close-btn js-nav-submit"><div class="btn__bg"></div><span class="btn__text">ок</span></button></div></div></div></div>`;
		let $link;
		let $menu = $('.content').append(mobMenu).find('.mobile-nav__content');
		let $submit = $menu.siblings().find('.js-nav-submit');

		// init modal
		Popup.expose($);
		this.$tabNav.popup({
			targetPopupId: 'menuPopup',
			closeOnEsc: false,
			closeBtnSelector: '.js-modal-close'
		});
		let modalMenu = $('[data-popup-id="menuPopup"]').data('popup');

		this.$tabNav.on('click tap', (ev) => {
			ev.preventDefault();
			copyLinks(this);
		});

		$submit.on('click tap', () => {
			let activeIndex = $menu.find('.is-active').index();
			let _this = this;
			let currentIndex = _this.getActiveIndex();
			if (activeIndex !== currentIndex) {
				window.location = _this.$tabNav.eq(activeIndex).attr('href');
			}
			modalMenu.close();
		});

		function copyLinks(el) {
			$link = $menu.html(el.$tabNav.clone().removeClass('st2-tabs__tabs-el').addClass('mobile-nav__el')).children();

			$link.on('click tap', (ev) => {
				ev.preventDefault();
				$link.removeClass('is-active');
				$(ev.currentTarget).addClass('is-active');
			});
		}
	}

	getActiveIndex() {
		let activeIndex = 0;

		this.$tabNav.each(function () {
			if ($(this).hasClass(css.active)) {
				activeIndex = $(this).index();
			}
		});

		return activeIndex;
	}
	
	setResult(result) {
		let $disabledInput = this.$form.find('input').filter('[data-input="result"]');
		
		$disabledInput
			.val(result)
			.addClass(css.fill)
			.attr('disabled', false);
	}
	
	removeResult() {
		let $disabledInput = this.$form.find('input').filter('[data-input="result"]');
		
		$disabledInput
			.val('')
			.removeClass(css.fill)
			.attr('disabled', true);
	}
}
