import slick from 'slick-carousel';

class Slider {

	constructor() {
		this.$slider = $('.js-slider');
		this.$mobileSlider = $('.js-mobile-slider');
		
		this.init();
	}
	
	init() {
		this.createSlider();
		this.createMobileSlider();
	}
	
	createMobileSlider() {
		this.$mobileSlider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			dots: true,
			infinite: true,
			arrows: false,
			speed: 400,
			cssEase: 'ease',
			useTransform: true,
			adaptiveHeight: true,
			mobileFirst: true,
			responsive: [
				{
					breakpoint: 767,
					settings: 'unslick'
				}
			]
		});
	}
	
	createSlider() {
		this.$slider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			dots: true,
			infinite: true,
			arrows: false,
			speed: 400,
			cssEase: 'ease',
			useTransform: true,
			adaptiveHeight: true
		});
	}

}

export default new Slider();
