import { css } from '../../modules/dev/helpers';

;(function () {
	if ($('.package-item').length) {
		$('.package-item__btn-more').on('click', function (e) {
			e.preventDefault();
			$(this).toggleClass(css.active);
			if ($(this).hasClass(css.active)) {
				TweenMax.set($(this).next('ul'), { height: 'auto' });
				TweenMax.from($(this).next('ul'), 0.5, { height: 0, ease: Power2.easeInOut });
			} else {
				TweenMax.to($(this).next('ul'), 0.5, { height: 0, ease: Power2.easeInOut });
			}
		});
	}
}());
