import {TimelineMax} from 'gsap';
import {Resp} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class Teaser3 {

	constructor() {
		this.$container = $('.main-teaser-3');
		this.$title = this.$container.find('.main-teaser-3__title');
		this.$subtitle = this.$container.find('.main-teaser-3__subtitle');
		this.$btn = this.$container.find('.main-teaser-3__btn');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

	_wrapHidden() {
		WrapHidden.addWrapClone(this.$title, { setWidthAuto: true, marginAuto: true });
		WrapHidden.addWrapClone(this.$subtitle, { setWidthAuto: true, marginAuto: true });
		WrapHidden.addWrap(this.$btn);
	}

	_createAnimation() {
		this.tl
			.addLabel('start')

			//title
			.from(this.$title, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$title.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'start')
			.addLabel('titleEnd')

			//subtitle
			.from(this.$subtitle, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'titleEnd-=0.5')
			.from(this.$subtitle.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'titleEnd-=0.5')
			.addLabel('subtitleEnd')

			//button animation
			.from(this.$btn, 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'subtitleEnd-=0.5')
			.from(this.$btn.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'subtitleEnd-=0.5')
			.addLabel('btnEnd');
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

}

export const Main13 = new Teaser3();
