import 'perfect-scrollbar/dist/js/perfect-scrollbar.jquery.min';

export class PerfectScroll {
	constructor() {
		this.$container = $('.js-perfect-scroll');
	}
	
	init() {
		this.initScroll();
		window.initScroll = initScroll.bind(this);
		
		function initScroll() {
			this.initScroll();
		}
	}
	
	initScroll() {
		this.$container.perfectScrollbar({
			wheelPropagation: true,
			swipePropagation: true,
			swipeEasing: false
		});
	}
}

export default (new PerfectScroll).init();
