import { css } from '../../modules/dev/helpers';

export class InfoPage {
	constructor() {
		this.$circle = $('.info-notification__el-circle');
		this.$card = $('.card');
	}

	init() {
		this.bindEvents();
	}

	bindEvents() {
		this.$circle.on('click tap', function () {
			$(this).toggleClass(css.disabled);
		});
	}
}

export default new InfoPage();
