import { Resp } from './helpers';
import WrapHidden from './wrapHidden';

export class Split {
	
	/**
	 * cache Dom
	 * @param {string} el
	 */
	constructor (el) {
		this.$el = $(el);
	}
	
	init () {
		if (Resp.isDesk) {
			this._split();
			this._wrapSplit();
		}
	}
	
	reInit () {
		this.init();
	}
	
	_split () {
		let lines,
		    item,
		    html;
		
		$.each(this.$el, function () {
			if ($(this).data('split-init') === 'true') return;
			
			//create ind
			$(this).attr('data-split-init', 'true');
			
			//clear html
			html = '';
			
			//line split br tag
			lines = $(this).html().split('<br>');
			
			//separate each line
			for (item in lines) {
				if (lines.hasOwnProperty(item)) {
					html += `<span class="split-line">${lines[item]}</span>`;
				}
			}
			$(this).html(html);
		});
	}
	
	_wrapSplit () {
		WrapHidden.addWrap(this.$el.children());
	}
	
}

export default new Split('.js-split');
