import 'slick-carousel';
import {TimelineMax, CSSPlugin} from 'gsap';
import {Resp} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class MainSlider {
	constructor() {
		this.$slider = $('.js-main-slider');
		this.$container = $('.wow-story');
		this.$title = this.$container.find('.wow-story__title');
		this.$slide = this.$slider.find('.main-slider__item');
		this.$images = this.$slider.find('.main-slider__images');
		this.$subtitle = this.$slider.find('.main-slider__title');
		this.$text = this.$slider.find('.main-slider__text');
		this.$btn = this.$container.find('.wow-story__btn');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		this._createSlider();
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}
	}

	_createSlider() {
		this.$slider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			dots: false,
			infinite: true,
			arrows: false,
			speed: 750,
			cssEase: 'ease-in-out',
			centerMode: true,
			centerPadding: '25%',
			focusOnSelect: true,
			useTransform: true,
			responsive: [
				{
					breakpoint: 1199,
					settings: {
						centerPadding: '10%',
						dots: true,
						adaptiveHeight: true
					}
				},
				{
					breakpoint: 767,
					settings: {
						centerPadding: '0%',
						dots: true,
						adaptiveHeight: true
					}
				}
			]
		});
	}

	_createAnimation() {
		//set styles before animation
		TweenMax.set(this.$title, {
			'margin-left': 'auto',
			'margin-right': 'auto',
			'padding-left': '10px',
			'padding-right': '10px'
		});

		let leftSlide = this.$slider.find('.main-slider__item').eq(1);
		let rightSlide = this.$slider.find('.main-slider__item').eq(3);

		this.tl
			.addLabel('start')

			//title animation
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'start')
			.from(this.$title, 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'start')
			.addLabel('titleEnd')

			//images animation
			.staggerFrom(this.$images.eq(0).children().children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 0.2, 'titleEnd-=0.5')
			.staggerFrom(this.$images.eq(0).children(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 0.2, 'titleEnd-=0.5')
			.addLabel('imagesEnd')

			//subtitle animation
			.from(this.$subtitle.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'imagesEnd-=0.5')
			.from(this.$subtitle, 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'imagesEnd-=0.5')
			.addLabel('subtitleEnd')

			//text animation
			.from(this.$text.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'subtitleEnd-=0.5')
			.from(this.$text, 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'subtitleEnd-=0.5')
			.addLabel('textEnd')

			//button animation
			.from(this.$btn, 1, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'textEnd-=0.5')
			.from(this.$btn.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'textEnd-=0.5')
			.addLabel('btnEnd')

			//right slide
			.from(rightSlide, 0.5, {
				autoAlpha: 0,
				x: 100,
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'btnEnd-=1')

			//left slide
			.from(leftSlide, 0.5, {
				autoAlpha: 0,
				x: -100,
				ease: Power3.easeOut,
				clearProps: 'transform'
			}, 'btnEnd-=1');
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addInnerWrap(this.$images.eq(0).children(), true);
		WrapHidden.addInnerWrap(this.$subtitle.eq(0), true);
		WrapHidden.addInnerWrap(this.$text.eq(0), true);
		WrapHidden.addWrap(this.$btn);
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}
}

export const Main09 = new MainSlider();
