import ScrollMagic from 'ScrollMagic';
import {TimelineMax} from 'gsap';
import {Resp, $window} from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class Trainer {

	constructor() {
		this.$container = $('.trainer');
		this.$desc = this.$container.find('.trainer__desc');
		this.$titleLarge = this.$container.find('.trainer__title-lg');
		this.$title = this.$container.find('.trainer__title');
		this.$ul = this.$container.find('ul');
		this.$p = this.$ul.next();
		this.$alphaVideo = this.$container.find('#alphaVideo');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		this._alphaVideoInView();
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._play();
		}
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$titleLarge);
		WrapHidden.addInnerWrap(this.$desc);
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addWrap(this.$ul);
		WrapHidden.addWrap(this.$p);
	}

	_alphaVideoInView() {
		if (this.$alphaVideo.length) {
			this.$alphaVideo.get(0).pause();
			this.alphaVideoController = new ScrollMagic.Controller();
			this.alphaVideoScene = new ScrollMagic.Scene({
				triggerElement: this.$container.get(0),
				triggerHook: 1,
				duration: this.$container.height() + $window.height() + 200,
				offset: -200
			})
				.on('enter', () => {
					this.$alphaVideo.get(0).play();
				})
				.on('leave', () => {
					this.$alphaVideo.get(0).pause();
				})
				.addTo(this.alphaVideoController);
		}
	}

	_createAnimation() {
		this.$canvas = this.$container.find('#outputAlphaVideo');
		this.tl
			.addLabel('start')
			.from(this.$titleLarge.children(), 1.25, {
				x: '-115%',
				ease: Power3.easeOut,
				clearProps: 'all'
			}, 'start')
			.from(this.$titleLarge, 1.25, {
				x: '-25%',
				ease: Power3.easeOut,
				clearProps: 'all'
			}, 'start')
			.addLabel('titleLargeEnd')
			.from(this.$desc.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'titleLargeEnd-=0.5')
			.from(this.$desc, 1, {
				x: '-75%',
				ease: Power3.easeOut,
			}, 'titleLargeEnd-=0.5')
			.addLabel('descEnd')
			.from(this.$canvas, 2, {
				alpha: 0,
				x: 200,
				ease: Power3.easeOut
			}, 'start')
			.from(this.$title.children(), 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'descEnd-=0.5')
			.from(this.$title, 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'descEnd-=0.5')
			.addLabel('titleEnd')
			.from(this.$ul, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'titleEnd-=0.5')
			.from(this.$ul.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
			}, 'titleEnd-=0.5')
			.addLabel('ulEnd')
			.from(this.$p, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'ulEnd-=0.5')
			.from(this.$p.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut
			}, 'ulEnd-=0.5')
			.addLabel('pEnd');
	}

	_play() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

}

export const Main03 = new Trainer();
