import { TimelineMax, CSSPlugin } from 'gsap';
import { Resp, css } from '../../../modules/dev/helpers';
import WrapHidden from '../../../modules/dev/wrapHidden';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class Fame {

	constructor() {
		this.$container = $('.fame');
		this.$table = this.$container.find('.fame-table');
		this.$btn = this.$container.find('.fame__btn');
		this.$slider = this.$container.find('.fame__table-row');
		this.$desc = this.$container.find('.fame__desc');
		this.$title = this.$container.find('.fame__title');
		this.$text = this.$container.find('.fame__text');
		this.image = 'fame-table__image';
		this.tl = new TimelineMax({ paused: true });
	}

	/**
	 * @return {{MIN_ROWS: number}}
	 */
	constants() {
		this.minRows = 3;
		this.fixHeight = 5;
	}

	init() {
		this._checkRows();

		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._playAnimation();
		}

		if (!Resp.isDesk) {
			this._createSlider();
		}
	}

	_wrapHidden() {
		WrapHidden.addWrapClone(this.$desc, { setWidthAuto: true });
		WrapHidden.addWrapClone(this.$title, { setWidthAuto: true });
		WrapHidden.addWrapClone(this.$text, { setWidthAuto: true });
		WrapHidden.addWrap(this.$btn);
	}

	_playAnimation() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			onStart: () => {
				this.tl.play();
			}
		});
	}

	_createAnimation() {
		this.tl
			.addLabel('start')

			//desc animation
			.from(this.$desc, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$desc.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'start')
			.addLabel('descEnd')

			//title animation
			.from(this.$title, 1, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'descEnd-=0.5')
			.from(this.$title.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'descEnd-=0.5')
			.addLabel('titleEnd')

			//text animation
			.from(this.$text, 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'titleEnd-=0.5')
			.from(this.$text.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'titleEnd-=0.5')
			.addLabel('textEnd')

			//tables animation
			.staggerFrom(this.$table, 1, {
				autoAlpha: 0,
				x: -100
			}, 0.25, 'textEnd-=0.5')
			.addLabel('tableEnd')

			//button animation
			.from(this.$btn, 1, {
				x: '-115%',
				ease: Power3.easeOut,
			}, 'tableEnd-=0.5')
			.from(this.$btn.parent(), 1, {
				x: '-75%',
				ease: Power3.easeOut,
				clearProps: 'overflow'
			}, 'tableEnd-=0.5')
			.addLabel('btnEnd');
	}

	_checkRows() {
		this.constants();
		let tableInd = false;
		
		this.$table.each((index, el) => {
			if ($(el).find('.fame-table__row').length > this.minRows) {
				tableInd = true;
				
				let activeItem = $(el).find('.is-active');
				
				if (activeItem.length) {
					let myText = $(el).data('my-result');
					let htmlText = `<div class="fame-table__image-text">${myText}</div>`;
					activeItem.find('.' + this.image).append(htmlText);
				}
				
				if (activeItem.length && activeItem.index() >= this.minRows) {
					let $container = $(el).find('.fame-table__body');
					$container.height($container.height() + 5);
				}
			}
		});
		
		if (tableInd) {
			//show button
			this.$btn.css('display', 'table');

			//bind button click
			this._bindEvents();
		}
	}

	_showAllRows() {
		if (!Resp.isDesk) {
			this.$container.find('.slick-list').height('auto');
		}
		this.$table.each((index, el) => {
			let fixFlag = false;
			let $container = $(el).find('.fame-table__body');
			let $rows = $(el).find('.fame-table__row');
			if ($rows.length > this.minRows) {
				let activeItem = $container.find('.is-active');
				if (activeItem.length && activeItem.index() >= this.minRows) {
					fixFlag = true;
				}
				let startHeight = $container.css({ overflow: 'hidden' }).height();
				let itemHeight = startHeight / this.minRows;
				itemHeight = fixFlag ? (startHeight - this.fixHeight) / (this.minRows + 1) : itemHeight;
				let targetHeight = $rows.length * itemHeight;

				$(el).addClass(css.active);
				$rows.addClass('is-flex');
				
				$container.css({ height: startHeight });
				TweenMax.to($container, 0.5, {
					height: targetHeight,
					ease: Power2.easeInOut,
					onComplete: () => {
						if (!Resp.isDesk) {
							this.$slider.slick('setPosition');
						}
					}
				});
			}
		});
	}

	_hideAllRows() {
		if (!Resp.isDesk) {
			this.$container.find('.slick-list').height('auto');
		}
		this.$table.each((index, el) => {
			let fixHeight = 5;
			let fixFlag = false;
			let $container = $(el).find('.fame-table__body');
			let $rows = $(el).find('.fame-table__row');
			if ($rows.length > this.minRows) {
				let activeItem = $container.find('.is-active');
				if (activeItem.length && activeItem.index() >= this.minRows) {
					fixFlag = true;
				}
				let startHeight = $container.css({ overflow: 'hidden' }).height();
				let itemHeight = startHeight / $rows.length;
				let targetHeight = this.minRows * itemHeight;
				targetHeight = fixFlag ? (this.minRows + 1) * itemHeight + fixHeight : targetHeight;
				
				$(el).removeClass(css.active);

				TweenMax.to($container, 0.5, {
					height: targetHeight,
					ease: Power2.easeInOut,
					onComplete: () => {
						$rows.eq(this.minRows - 1).nextAll().css({ display: 'none' });
						$rows.removeClass('is-flex');
						if (!Resp.isDesk) {
							this.$slider.slick('setPosition');
						}
					}
				});
			}
		});
	}

	_bindEvents() {
		this.$btn.children().on('click tap', (e) => {
			e.preventDefault();
			this.$btn
				.children().children().last()
				.data('active', true);
			if (this.$btn.data('active') !== true) {
				this.$btn.data('active', true);
				this._showAllRows();
			} else {
				this.$btn.data('active', false);
				this._hideAllRows();
			}
		});
	}

	_createSlider() {
		const activeIndex = this.$slider.children().filter('.' + css.active).index();
		
		this.$slider.slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			dots: true,
			infinite: true,
			arrows: false,
			speed: 400,
			cssEase: 'ease',
			useTransform: true,
			adaptiveHeight: true
		});

		this.$slider.find('.slick-dots').insertAfter(this.$btn);
		this.$slider.slick('slickGoTo', activeIndex, false);
	}

}

export const Main10 = new Fame();
