import { Resp } from '../../../modules/dev/helpers';
import { TimelineMax } from 'gsap';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';

export default class ResultsSection {

	constructor() {
		this.$scope = $('.results-section');
		this.$container = this.$scope.find('.results-item');
		this.tl = [];
	}

	init() {
		if (Resp.isDesk) {
			this.createAnim();
		}
	}

	playAnim() {
		if (Resp.isDesk) {
			$.each(this.tl, (index, tl) => {
				new SCROLL_TRIGGER_ANIMATIONS({
					container: this.$container.eq(index).get(0),
					triggerHook: 1,
					onEnter: () => {
						tl.play();
					},
					onEnd: () => {
						tl.reverse().play();
					}
				});
			});
		}
	}

	createAnim() {
		this.$container.each((index, container) => {
			let $img = $(container).find('.results-item__col_img img');
			let $name = $(container).find('.results-item__col_text > p:first-child');
			let $text = $name.nextAll().not('.results-item__row');
			let $before = $(container).find('.results-item__row-col:first-child');
			let $after = $before.next();
			let tl = new TimelineMax({ paused: true });

			// create timeline
			tl

				// img
				.staggerFrom($img, 0.75, {
					y: 50,
					autoAlpha: 0,
					ease: Power4.easeOut
				}, 0.1)

				// name
				.from($name, 0.75, {
					y: 50,
					autoAlpha: 0,
					ease: Power4.easeOut
				}, '-=0.5')

				// text
				.from($text, 0.75, {
					y: 50,
					autoAlpha: 0,
					ease: Power4.easeOut
				}, '-=0.5')

				// before
				.from($before, 0.75, {
					y: 50,
					autoAlpha: 0,
					ease: Power4.easeOut
				}, '-=0.5')

				// after
				.from($after, 0.75, {
					y: 50,
					autoAlpha: 0,
					ease: Power4.easeOut
				}, '-=0.5');

			this.tl.push(tl);
		});
	}

}

export const Results01 = new ResultsSection();
