import {Resp} from './helpers';

export class WrapHidden {

	init() {
		if (Resp.isDesk) {
			this._cacheDom();
			this._wrapClone(this.$el);
		}
	}

	reInit() {
		this.init();
	}

	_cacheDom() {
		this.$el = $('[data-wrap-hidden]');
	}

	addInnerWrap(el, state) {
		this._wrapInner(el, state);
	}

	addWrap(el, state = false) {
		this._wrap(el, state);
	}

	/**
	 * Add parent wrap as clone
	 * @param {jQuery} el
	 * @param {Object} config {setWidthAuto, marginAuto}
	 */
	addWrapClone(el, config = {}) {
		this._wrapClone(el, config);
	}

	_wrapClone(el, config) {
		el.each(function () {
			if ($(this).data('wrap-hidden') === 'false') return;

			$(this)
				.data('wrap-hidden', 'false')
				.wrap('<div class="wrap-hidden"></div>');

			// set margin auto if data-attr exists
			if ($(this).data('margin-auto') === true) {
				config.marginAuto = true;
			}

			// set width auto if data-attr exists
			if ($(this).data('width-auto') === true) {
				config.setWidthAuto = true;
			}

			$(this)
				.parent()
				.css({
					display: $(this).css('display'),
					marginTop: $(this).css('marginTop'),
					marginLeft: config.marginAuto ? 'auto' : $(this).css('marginLeft'),
					marginRight: config.marginAuto ? 'auto' : $(this).css('marginRight'),
					marginBottom: $(this).css('marginBottom'),
					padding: $(this).css('padding'),
					width: config.setWidthAuto ? 'auto' : $(this).css('width'),
					'max-width': $(this).css('max-width'),
					overflow: 'hidden'
				});

			$(this).css({
				margin: 0,
				padding: 0
			});
		});
	}

	_wrap(el, state) {
		el.each(function () {
			if ($(this).data('wrap-hidden') === 'false') return;

			$(this)
				.data('wrap-hidden', 'false')
				.wrap('<div class="wrap-hidden"></div>');

			if ($(this).css('display') === 'table' && state === true) {
				$(this)
					.parent()
					.css({
						display: $(this).css('display'),
						overflow: 'hidden'
					});
			} else {
				$(this)
					.parent()
					.css({
						display: $(this).css('display'),
						'padding-left': '10px',
						'padding-right': '10px',
						'margin-left': '-10px',
						'margin-right': '-10px',
						overflow: 'hidden'
					});
			}
		});
	}

	_wrapInner(el, state = false) {
		el.each(function () {
			if ($(this).data('wrap-hidden') === 'false') return;

			if (state) {
				$(this)
					.data('wrap-hidden', 'false')
					.css({
						overflow: 'hidden'
					})
					.wrapInner('<div class="parent-hidden"></div>');
			} else {
				$(this)
					.data('wrap-hidden', 'false')
					.css({
						overflow: 'hidden',
						'padding-left': '10px',
						'padding-right': '10px',
						'margin-left': '-10px',
						'margin-right': '-10px',
					})
					.wrapInner('<div class="parent-hidden"></div>');
			}
		});
	}

}

export default new WrapHidden();
