import 'jquery-form-validator';
import 'jquery-form-validator/form-validator/security';
import { $window, css } from '../modules/dev/helpers';

export class Validate {
	constructor() {
		this.inputText = '.form-control-text';
		this.submit = '[type="submit"]';
		this.textarea = 'textarea.form-control-text';
		this.thanks = '.js-submit-ty';
		this.scroll = '.popup__subtitle span';
		this.clearBtn = 'form-control-clear';
	}
	
	init() {
		this.resizeTextarea();
		Validate.initValidator();
		this.initPopupThanks();
		this.initScrollBtn();
		this.onFocusOut();
		this.checkInputFill();
		$window.on('load', () => {
			this.checkInputFill();
		});
	}
	
	initScrollBtn() {
		if ($(this.scroll).length && $('#faq').length) {
			$(this.scroll).on('click', function () {
				setTimeout(() => {
					$('body').animate({ scrollTop: $('#faq').offset().top }, 500);
				}, 500);
			});
		}
	}
	
	initPopupThanks() {
		if ($(this.thanks).length) {
			let speed = 200;
			$(this.thanks).on('submit', function (e) {
				e.preventDefault();
				$(this).parent().siblings().andSelf().not(':last-child').fadeOut(speed);
				setTimeout(() => {
					$(this).parent().siblings().last().fadeIn(speed);
				}, speed);
			});
		}
	}
	
	onFocusOut() {
		$(this.inputText).each(function () {
			let $self = $(this);
			if ($self.hasClass('js-no-error')) {
				$self.blur(() => {
					$self.parent().removeClass('has-error');
				});
			}
		});
		
		$(this.submit).each(function () {
			let $self = $(this);
			if ($self.hasClass('js-no-error')) {
				$self.blur(() => {
					$self.closest('form').find('.has-error').each(function () {
						$(this).removeClass('has-error');
					});
				});
			}
		});
	}
	
	resizeTextarea() {
		$.each($(this.textarea), function () {
			let resizeTextarea = function (el) {
				$(el).css('height', '').css('height', el.scrollHeight);
			};
			resizeTextarea(this);
			$(this).on('keyup input', function () { resizeTextarea(this); });
		});
	}
	
	checkInputFill() {
		$(this.inputText).each(function () {
			checkInput($(this));
		});
		$(this.inputText).blur(function () {
			checkInput($(this));
		});
		
		function checkInput(el) {
			if (el.val() !== '') {
				el.addClass(css.fill);
			} else {
				el.removeClass(css.fill);
			}
		}
	}
	
	static initValidator() {
		$.validate({
			validateOnBlur: true,
			showHelpOnFocus: false,
			addSuggestions: false,
			scrollToTopOnError: false,
			borderColorOnError: false,
			validateOnEvent: true,
			modules: 'security'
		});
	}
}

export default new Validate();

window.refreshValidate = function () {
	new Validate().init();
};
