/**
 * Qa page scripts.
 *
 * @module Results
 */

/** Import scripts */
import { TweenMax, CSSPlugin } from 'gsap';
import { css } from '../../modules/dev/helpers';
import { ELEMENTS } from '../../modules/dev/elements';

/** Class representing a website's Qa page functions. Initialized only on web site's qa page. */
export class Qa {

	initFaq() {
		if ($(ELEMENTS.faq.container).length) {
			let $item = $(ELEMENTS.faq.item);
			let $btn = $(ELEMENTS.faq.btn);
			let $content = $(ELEMENTS.faq.content);
			let speed = 0.3;

			$btn.on('click', function () {
				let $selfItem = $(this).closest($item);
				let $selftContent = $selfItem.find($content);

				$item.not($selfItem).removeClass(css.active);
				$content.each(function () {
					if (!$(this).closest($selfItem).length) {
						hideContent($(this));
					}
				});
				$selfItem.toggleClass(css.active);

				if ($selfItem.hasClass(css.active)) {
					showContent($selftContent);
				} else {
					hideContent($selftContent);
				}
			});

			function showContent(el) {
				TweenMax.set(el, { height: 'auto' });
				TweenMax.from(el, speed, { height: 0 });
			}

			function hideContent(el) {
				TweenMax.to(el, speed, { height: 0 });
			}
		}
	}

	init() {
		this.initFaq();
	}

}

export default new Qa();
