/**
 * Calculator page scripts.
 *
 * @module Results
 */

/** Import scripts */
import 'js/modules/dep/ion.rangeSlider.min';
import ProgramPower from '../main/sections/08_program-power';

/** Class representing a website's Calculator page functions. Initialized only on web site's calculator page. */
export class ProgramPowerCalculator extends ProgramPower {}

export class Calculator {

	init() {
		this.initCalc();
		Calculator.initProgramPower();
	}

	static initProgramPower() {
		(new ProgramPowerCalculator()).init();
	}

	initCalc() {
		if ($('.js-calc-weight').length) {
			let $resultOutput = $('.calculator__col-result');

			// weight slider
			let $weightOutput = $('.js-calc-weight').find('.calc__title span:nth-child(2)');
			let formula = $resultOutput.data('formula');

			$('.js-calc-weight').find('input').ionRangeSlider({
				min: 40,
				max: 140,
				from: 70,
				hide_min_max: true,
				hide_from_to: true,
				grid: true,
				grid_num: 10,
				onStart: function (data) {
					$weightOutput.text(data.from);
				},
				onChange: function (data) {
					$weightOutput.text(data.from);
					$resultOutput.text(getResult());
				}
			});

			$resultOutput.text(getResult());

			function getResult() {
				let result;
				let weight = +$weightOutput.text();

				result = eval(formula);
				result = Math.round(result * 10) / 10;

				return result;
			}
		}
	}

}

export default new Calculator();
