import Popup from 'vintage-popup';

class Popups {
	constructor() {
		this.$popup = $('[data-popup-target]');
		
		this.init();
	}

	init() {
		this.$popup.on('click tap', function (e) {
			e.preventDefault();
		});
		
		$('.js-modal-close').on('click tap', function (e) {
			e.preventDefault();
		});
		
		$(document).on('click', '.js-popup-close', function () {
			Popup.closeAllPopups();
		});
		
		this.$popup.popup({
			closeOnEsc: false,
			closeBtnSelector: '.js-popup-close, .popup__subtitle span, .js-modal-close',
			beforeOpen: (ev) => {
				let $playBtn = ev.$popup.find('.modal__video-play-btn');
				
				if ($playBtn.length) {
					this.initPlayBtn($playBtn);
				}
				
				window.initScroll();
				
				//pause all videos
				$('video').each((index, el) => {
					if (!el.paused) el.pause();
				});
			}
		});
	}

	initPlayBtn(el) {
		el.on('click tap', function () {
			let $this = $(this);
			
			$this.addClass('is-hidden');
			if ($this.siblings('video').length) {
				$this.siblings('video').attr('controls', 'true').get(0).play();
				$this.siblings('video').focus();
			}
		});
	}
}

export default new Popups();

window.Modal = {
	reinit: function () {
		new Popups();
	},
	open: function (target) {
		let modalEl = $(`[data-popup-id="${target}"]`).data('popup');
		
		modalEl.open();
	},
	close: function (target) {
		let modalEl = $(`[data-popup-id="${target}"]`).data('popup');
		
		modalEl.close();
	}
};
