import {TimelineMax, CSSPlugin} from 'gsap';
import {Resp, $window} from '../../../modules/dev/helpers';
import SCROLL_TRIGGER_ANIMATIONS from '../../../modules/dev/animation/helpers/_scrollTriggerAnimations';
import WrapHidden from '../../../modules/dev/wrapHidden';

export default class AboutProgram {

	constructor() {
		this.$container = $('.about-program');
		this.$lineTop = this.$container.find('.about-program__video-line-top');
		this.$videoContainer = this.$container.find('.about-program__video');
		this.$lineBot = this.$container.find('.about-program__video-line-bot');
		this.$title = this.$container.find('h2');
		this.$paragraph = this.$container.find('p');
		this.$btn = this.$container.find('.about-program__btn');
		this.tl = new TimelineMax({ paused: true });
	}

	init() {
		if (Resp.isDesk) {
			this._wrapHidden();
			this._createAnimation();
			this._play();
		}

		if (Resp.isMobile) {
			if (this.$btn.length) {
				this.swapBtn();
			}
		}
	}

	_getHeight() {
		return this.$videoContainer.height();
	}

	_wrapHidden() {
		WrapHidden.addInnerWrap(this.$title);
		WrapHidden.addInnerWrap(this.$paragraph);
		if (this.$btn.length) {
			WrapHidden.addWrap(this.$btn);
		}
	}

	_createAnimation() {
		let height = this._getHeight(),
			heightBot = 0.85 * height,
			thisHeight;

		this.tl
			.addLabel('start')

			// title
			.from(this.$title.children(), 1, {
				x: '115%',
				ease: Power3.easeOut,
			}, 'start')
			.from(this.$title, 1, {
				x: '75%',
				ease: Power3.easeOut,
			}, 'start')
			.addLabel('titleEnd')

			// paragraph
			.staggerFrom(this.$paragraph.children(), 1, {
				x: '115%',
				ease: Power3.easeOut
			}, 0.15, 'titleEnd-=0.5')
			.staggerFrom(this.$paragraph, 1, {
				x: '25%',
				ease: Power3.easeOut
			}, 0.15, 'titleEnd-=0.5')
			.addLabel('paragraphEnd')

			// button
			.from(this.$btn, 0.75, {
				x: '130%',
				ease: Power3.easeOut
			}, 'paragraphEnd-=0.5')
			.from(this.$btn.parent(), 0.75, {
				x: '25%',
				ease: Power3.easeOut
			}, 'paragraphEnd-=0.5')

			// lines
			.to(this.$lineTop, 1.25, {
				css: {
					transform: function (index, target) {
						thisHeight = index % 2 === 0 ? -height : height;
						return `skewX(-9deg) translateY(${thisHeight}px) translateX(0)`;
					}
				},
				ease: Power4.easeInOut,
			}, 'start')
			.to(this.$lineBot, 1.25, {
				css: {
					transform: function (index, target) {
						thisHeight = index % 2 === 0 ? heightBot : -heightBot;
						return `skewX(-9deg) translateY(${thisHeight}px) translateX(0)`;
					}
				},
				ease: Power4.easeInOut,
			}, 'start');
	}

	_play() {
		new SCROLL_TRIGGER_ANIMATIONS({
			container: this.$container.get(0),
			triggerHook: 0.6,
			onStart: () => {
				this.tl.play();
			}
		});
	}

	swapBtn() {
		if (!this.$btn.is(':hidden')) {

		}
	}

}

export const Main02 = new AboutProgram();
