import { $window, $header, currentPage, Resp, throttle } from '../helpers';
import { TimelineMax } from 'gsap';
import WrapHidden from '../wrapHidden';

export class Header {

	constructor() {
		this.$header = $header;
		this.$logo = this.$header.find('.header__logo');
		this.$controlsBtn = this.$header.find('.header__controls-btn').children();
		this.tl = new TimelineMax({ paused: true });

		this.init();
	}

	init() {
		if (Resp.isDesk) {
			if (currentPage !== 'main') {
				if (this.$header.data('animation') !== false) {
					this.createAnim();
				}
			}
		}
		if (Resp.isMobile) {
			this.fixedHeader();
		}
	}

	playAnim() {
		if (Resp.isDesk) {
			if (currentPage !== 'main') {
				this.tl.play();
			}
		}
	}

	fixedHeader() {
		toggleHeader();
		const toggleHeaderScroll = throttle(() => {
			toggleHeader();
		}, 250, this);

		function toggleHeader() {
			if ($window.scrollTop() > 0) {
				$header.addClass('is-fixed');
			} else {
				$header.removeClass('is-fixed');
			}
		}

		//refresh page on resize
		$window.on('scroll', toggleHeaderScroll);
	}

	createAnim() {
		// wrap
		WrapHidden.addInnerWrap(this.$logo);
		WrapHidden.addWrap(this.$controlsBtn);

		// create timeline
		this.tl
			.addLabel('start')
			.from(this.$logo.children(), 0.75, {
				x: '-115%',
				ease: Power3.easeOut
			}, 'start')
			.from(this.$logo, 0.75, {
				x: '-25%',
				ease: Power3.easeOut
			}, 'start')
			.addLabel('logoEnd')
			.staggerFrom(this.$controlsBtn, 0.75, {
				x: '115%',
				ease: Power3.easeOut
			}, 0.15, 'logoEnd-=0.35')
			.staggerFrom(this.$controlsBtn.parent(), 0.75, {
				x: '25%',
				ease: Power3.easeOut
			}, 0.15, 'logoEnd-=0.35');
	}
}

export default new Header();
